import React, { useEffect, useState } from 'react'
import { Box, Typography } from '@mui/material'
import { MuiFileInput } from 'mui-file-input'
import { themePalette } from '../../config/MaterialUi/Theme.Object'


type UploadFileProps = {
    value: File | undefined,
    image: boolean,
    handleValue: (value: File | undefined, name: string) => void,
    type: string,
    src?: string,
    name: string
}

const UploadFile: React.FC<UploadFileProps> = ({value,image,handleValue,type,name,src}) => {
    const [objectURL, setObjectURL] = useState<string | undefined>(src)

    useEffect(() => {
      return () => {
        if (objectURL) {
            URL.revokeObjectURL(objectURL)
        } 
      };
    }, [objectURL])


    useEffect(() => {
        if (value) {
          setObjectURL(URL.createObjectURL(value))
        } else {
          setObjectURL(src)
        }
      }, [value, src])

    return (
        <>
            {   image && (
                    <Box
                        component="div"
                        sx={{
                            display: 'flex',
                            mb: 2,
                            flexDirection: 'column',
                            justifyContent: 'center',
                            alignItems: 'center',
                            height: '200px',
                            width: '100%',
                            border: '2px dashed gray',
                            borderRadius: '10px',
                            cursor: 'pointer',
                            position: 'relative',
                            overflow: 'hidden',
                        }}
                    >
                        { 
                            (!objectURL)
                                ?  (
                                    <Typography variant="body1" textAlign="center" color="textSecondary">
                                        Imagen
                                    </Typography>
                                )  
                                : (
                                    <img
                                        // src={(objectURL ? objectURL : URL.createObjectURL(value))}
                                        src={objectURL}
                                        alt="Imagen"
                                        style={{
                                            position: 'absolute',
                                            top: '50%',
                                            left: '50%',
                                            transform: 'translate(-50%, -50%)',
                                            maxHeight: '100%',
                                            width: '100%',
                                        }}
                                    />
                                )
                        }
                    
                    </Box>
                )
            }
            <MuiFileInput                                 
                label="Seleccione Imagen"
                value={value}
                onChange={(file: File|null) => {
                    if (file && file.type.startsWith(`${type}`)){
                        handleValue(file, name)
                        setObjectURL(URL.createObjectURL(file))
                    } else
                        handleValue(undefined, name)
                        setObjectURL(undefined)
                }}
                hideSizeText
                sx={{ borderColor: themePalette.PRIMARY }}
            />
        </>
    )
}

export default UploadFile