import { useContext, useEffect, useState } from "react"
import ModalAdd from "./ModalAdd"
import { Button, Typography } from '@mui/material'
import { DataTableContext } from "../../../context/DataTableContext"
import { RowServices } from "../../../types/DataTable"
import useRequestApi, { MetodoHTTP } from "../../../hooks/useRequestApi"
import { ResponseFormat } from "../../../types/API/Index"
import { DeleteServiceRequestApi, DeleteServiceResponseApi } from "../../../types/API/Services"
import { Toastification } from "../../../context/ToastificationContext"

type DeleteServiceProps = {
    showDelete: boolean,
    setShowDelete: (value: boolean) => void
}

const DeleteService: React.FC<DeleteServiceProps> = ({ showDelete, setShowDelete }) => {
    const [title, setTitle] = useState<string>('Desactivar')
    const [isDelete, seIsDelete] = useState<boolean>(true)

    const { 
        currentRowSelected,
        setDataContext,
        dataAux
    } = useContext(DataTableContext)

    const {
        updateNotificationOpen,
        updateMessage
    } = useContext(Toastification)

    useEffect(() => {
        const currentRow = currentRowSelected as RowServices
        if (currentRow?.estado === 'INACTIVO') {
            setTitle('Activar')
            seIsDelete(false)
        } else{
            setTitle('Inactivar')
            seIsDelete(true)
        }
    }, [currentRowSelected])
    

    const { submit } = useRequestApi<ResponseFormat<DeleteServiceResponseApi>,DeleteServiceRequestApi>({
        path: `/Servicio/DeleteService`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const handleDelete = async () => {
        const currentRow = currentRowSelected as RowServices
        const response = await submit({
            id: currentRow.id,
            delete: isDelete
        })

        if (!response)
            return
        const { succeeded, message } = response
        
        if (!succeeded)
            return
        const data = (dataAux as RowServices[]).filter( service => {
            let auxService: RowServices = service
            return auxService.id !== currentRow.id
        })
        setDataContext(data)
        updateMessage(message)
        updateNotificationOpen(true)
    }
  
    return (
        <ModalAdd
            show={showDelete}
            setShow={setShowDelete}
        >
            <h1>{title} Servicio</h1>
            <Typography
                variant='body1'
            >
                Esta seguro de {title} este Servicio
            </Typography>
            <Button
                type='button'
                variant='contained'
                color={isDelete ? 'error' : 'primary'}
                onClick={handleDelete}
            >
                {title}
            </Button>
        </ModalAdd>
    )
}

export default DeleteService