import { useState } from 'react';
import { Options } from '../constants/masterDashboardConstants';
import { Main } from '../utils/components/Main';
import { DrawerHeader } from '../utils/components/Drawer';
import { Box } from '@mui/material';
import NavBar from '../components/NavBar'
import SideBar from '../components/SideBar'
import { Outlet } from 'react-router-dom';
import { themePalette } from '../config/MaterialUi/Theme.Object';
import { DataTableProvider } from '../context/DataTableContext';

type DashboardProps = {
    children: React.ReactNode
}

const Dashboard: React.FC<DashboardProps> = ({children}) => {

    const [open, setOpen] = useState<boolean>(true);

    return (
        <DataTableProvider>
            <Box sx={{ display: 'flex' }}>
                <NavBar
                    open={open}
                    setOpen={setOpen}
                >
                    {children}
                </NavBar>
                <SideBar
                    open={open}
                    setOpen={setOpen}
                    options={Options}
                />
                <Main open={open}>
                    <DrawerHeader 
                        background={ themePalette.BG }
                    />
                    <Box
                        sx={{ display: 'flex', flexDirection: 'column' ,alignItems: 'center' }}
                    >
                        <Outlet/>
                    </Box>
                </Main>
            </Box>
        </DataTableProvider>
    )
}

export default Dashboard
