/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from 'react'
import { Autocomplete, Box, Button, FormControlLabel, FormGroup, Grid, Switch, TextField } from '@mui/material'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { GetServicesRequestApi, GetServicesResponse, GetServicesResponseApi, MetodologiaResponse, MuestraResponse, ParametrosResponse } from '../../../types/API/Services'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { validationSchemaRequest } from '../../../constants/ValidationSchemas'
import { initialValueRequest } from '../../../constants/FormConstants'
import { DataForm, valueFormRequest } from '../../../types/Form'
import { themePalette } from '../../../config/MaterialUi/Theme.Object'
import { ChangeRequestStatusRequestApi, ChangeRequestStatusResponseApi, CreateRequestApi, CreateRsponseApi, Request, UpdateRequestApi } from '../../../types/API/Request'
import { UserData } from '../../../types/DataUser'
import { Toastification } from '../../../context/ToastificationContext'
import { GetLaboratoriesRequestApi, GetLaboratoriesResponse, GetLaboratoriesResponseApi } from '../../../types/API/Laboratories'

type AddRequestFormProps = {
    idRequest: string | undefined,
    currentRequest?: Request
}

const EditRequestFormAS: React.FC<AddRequestFormProps> = ({idRequest, currentRequest}) => {

    const [searchValueService, setSearchValueService] = useState<string| undefined>(undefined)
    const [searchValueLaboratory, setSearchValueLaboratory] = useState<string| undefined>(undefined)
    const [currentService, setCurrentService] = useState<number>()
    const [currentMethology, setCurrentMethology] = useState<number>()
    const [currentSample, setCurrentSample] = useState<number>()
    const [currentParam, setCurrentParam] = useState<number>()
    const [currentLaboratory, setCurrentLaboratory] = useState<GetLaboratoriesResponse>()
    const [isSampling, setIsSampling] = useState<boolean>(false)
    const [isAcreditado, setIsAcreditado] = useState<boolean>(false)
    // values for select
    const [optionsServices, setOptionsServices] = useState<GetServicesResponse[]>([])
    const [optionsMethology, setOptionsMethology] = useState<MetodologiaResponse[]>([])
    const [optionsSample, setOptionsSample] = useState<MuestraResponse[]>([])
    const [optionsParams, setOptionsParams] = useState<ParametrosResponse[]>([])
    const [optionsLaboratories, setOptionsLaboratories] = useState<GetLaboratoriesResponse[]>([])

    const [userData, setuserData] = useState<UserData>()

    const useRequestApiUpdate = useRequestApi<ResponseFormat<CreateRsponseApi>,CreateRequestApi>({
        path: `/Solicitud/UpdateRequest/${currentRequest?.idsolsolicitud??0}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.PUT
    })

    const requestService = useRequestApi<ResponseFormat<GetServicesResponseApi>,GetServicesRequestApi>({
        path: '/Servicio/GetServices/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestLaboratories = useRequestApi<ResponseFormat<GetLaboratoriesResponseApi>,GetLaboratoriesRequestApi>({
        path: '/Laboratorio/GetLaboratories',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestChangeRequestStatus = useRequestApi<ResponseFormat<ChangeRequestStatusResponseApi>,ChangeRequestStatusRequestApi>({
        path: `/Solicitud/ChangeRequestStatus`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const {
        updateNotificationOpen,
        updateTypeNotification,
        updateMessage
    } = useContext(Toastification)

    const handleSubmit = async (dataForm: DataForm) => {
        const dataAux = dataForm as valueFormRequest
        const { description, methology, param, sample, service } = dataAux
        console.log('request value form: ', dataAux)

        
        if ( idRequest && currentRequest) {
            const { idsolsolicitud } = currentRequest
            const payload: UpdateRequestApi = {
                acreditado: isAcreditado,
                muestreo: isSampling,
                muestraId: sample?.idservmuestra??0,
                muestra: sample?.nombre??'',
                metodologiaId: methology?.idservmetodologia??0,
                metodologia: methology?.nombre??'',
                parametroId: param?.idservparametroservicio??0,
                parametro: param?.nombre??'',
                servicioId: service?.servicio.idservservicio??0,
                servicio: service?.servicio.nombre??'',
                descripcion: description,
                clienteId: currentRequest.clienteId??0,
                cliente: currentRequest.cliente??'',
                idsolsolicitud,
                laboratorio: currentLaboratory?.laboratorio?.nombre?? '',
                laboratorioAsignadoId: currentLaboratory?.laboratorio?.idlablaboratorios
            }
            console.log('payload: ', payload)
            const response = await useRequestApiUpdate.submit(payload)

            const { laboratorioAsignadoId, estadoId,  } = currentRequest
            let payloadChangeStatusRequest = {
                laboratorioId: currentLaboratory?.laboratorio?.idlablaboratorios ?? 0,
                estadoId: estadoId ?? 0,
                solicitudId: idsolsolicitud ?? 0,
                usuarioSilabId: userData?.useraccountid,
            }
            if (currentLaboratory?.laboratorio?.idlablaboratorios){
                payloadChangeStatusRequest = {
                    laboratorioId: currentLaboratory?.laboratorio?.idlablaboratorios ?? 0,
                    estadoId: 3,
                    solicitudId: idsolsolicitud ?? 0,
                    usuarioSilabId: userData?.useraccountid,
                }
            }
            await requestChangeRequestStatus.submit(payloadChangeStatusRequest)
            if (!response)
                return

            const { message } = response

            updateMessage(message)
            updateNotificationOpen(true)
            setTimeout(() => {
                updateTypeNotification(true)
                updateNotificationOpen(false)
                window.location.href = '/Asistente-Silab/solicitudes/'
            } ,1000)
        }
    }

    useEffect(() => {
        const asynRequestLaboratories = async () => {
            const response = await requestLaboratories.submit({
                onlyActive: true,
                pageSize: 10,
                pageNumber: 1,
                orderById: false,
                orderByName: false,
                orderByStatus: false,
                orderByDescription: false,
                search: ''
            })

            if (!response)
                return

            const { data } = response

            if (!data)
                return

            const { items } = data

            const itemsAux = items.filter( lab => {
                const { servicios } = lab
                const srv = servicios.find( srv => srv.idservservicio === currentService)
                return srv ? true : false
            } )

            const auxCurrentLaboratory = itemsAux.find( lab => lab.laboratorio.idlablaboratorios === currentRequest?.laboratorioAsignadoId )
            setCurrentLaboratory(auxCurrentLaboratory)
            
            setOptionsLaboratories(itemsAux)
        }
        asynRequestLaboratories()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchValueLaboratory,currentService])

    useEffect(() => {
        const requestServices = async () => {
            const responseServices = await requestService.submit({
                search: searchValueService ?? '',
                pageNumber: 1,
                pageSize: 5,
                orderById: false,
                orderByName: false,
                orderByStatus: false,
                orderByDescription: false,
                onlyActive: false,
            })
            if ( responseServices ) {
                const { data } = responseServices
                if (!data)
                    return
                const { items } = data
                setOptionsServices(items)
            }
        }

        requestServices()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchValueService])


    useEffect(() => {
        if ( idRequest && currentRequest) {
            const { metodologiaId, muestraId, parametroId, servicioId, descripcion, muestreo, acreditado, laboratorioAsignadoId } = currentRequest

            console.log('current request: ', currentRequest)
            setIsSampling(muestreo)
            setIsAcreditado(acreditado)
            handleOtherFieldChange(descripcion,'description')
            const result = optionsServices.find( service => {
                const { servicio } = service
                return (servicio.idservservicio === servicioId) && service
            })
            setCurrentService(result?.servicio.idservservicio)
            formik.setFieldValue('service', result || null);
            if (result){
                setOptionsMethology(result.metodologias)
                setOptionsSample(result.muestras)
                setOptionsParams(result.parametros)
            }
            setCurrentMethology(metodologiaId)
            setCurrentSample(muestraId)
            setCurrentParam(parametroId)
            const currentLaboratoryAux = optionsLaboratories.find( laboratory => laboratory.laboratorio.idlablaboratorios === laboratorioAsignadoId)
            setCurrentLaboratory(currentLaboratoryAux)
        }
    }, [optionsServices,idRequest])

    useEffect(() => {
        if( currentMethology && currentParam && currentSample ){
            const currentMehologyAux = optionsMethology.find( methology => ( methology.idservmetodologia === currentMethology) )
            const currentSampleAux = optionsSample.find( sample => (sample.idservmuestra === currentSample) )
            const currentParamAux = optionsParams.find( param => (param.idservparametroservicio === currentParam))
            formik.setFieldValue('methology', currentMehologyAux || null);
            formik.setFieldValue('sample', currentSampleAux || null);
            formik.setFieldValue('param', currentParamAux || null);
        }
    }, [currentMethology])

    useEffect(() => {
        const userDataString = localStorage.getItem('UserData')
        if (!userDataString)
            return
        const jsonUserData = JSON.parse(userDataString)
        
        setuserData(jsonUserData as UserData)
        setSearchValueService('')
        setSearchValueLaboratory('')

    },[])

    const {
        handleTextFieldBlur,
        handleOtherFieldChange,
        HandleTextFieldChange,
        formik
    } = useFormValidateHook({
        initialValues: initialValueRequest,
        validationSchema: validationSchemaRequest,
        onSubmit: handleSubmit
    }) 

    return (
        <form onSubmit={formik.handleSubmit}>
            <Grid container spacing={2}>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="services"
                            options={optionsServices}
                            getOptionLabel={(option) => option.servicio.nombre}
                            value={currentService !== undefined ? optionsServices.find((option) => option.servicio.idservservicio === currentService) : null}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Servicios"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.service && Boolean(formik.errors.service)}
                                    helperText={formik.touched.service && formik.errors.service}
                                />
                            )}
                            onChange={(event, newInputValue: GetServicesResponse | null) => {
                                setCurrentService(undefined)
                                if (newInputValue){
                                    setCurrentService(newInputValue.servicio.idservservicio)
                                    setOptionsMethology(newInputValue.metodologias)
                                    setOptionsSample(newInputValue.muestras)
                                    setOptionsParams(newInputValue.parametros)
                                } else {
                                    setOptionsMethology([])
                                    setOptionsSample([])
                                    setOptionsParams([])
                                    setCurrentMethology(undefined)
                                    setCurrentParam(undefined)
                                    setCurrentSample(undefined)
                                    handleOtherFieldChange(null,"methology")
                                    handleOtherFieldChange(null,"sample")
                                    handleOtherFieldChange(null,"param")
                                }                                
                                handleOtherFieldChange(newInputValue,"service")
                            }}
                            isOptionEqualToValue={(option, value) => option.servicio.idservservicio === value.servicio?.idservservicio}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="methology"
                            options={optionsMethology}
                            getOptionLabel={(option) => option.nombre}
                            value={currentMethology !== undefined ? optionsMethology.find((option) => option.idservmetodologia === currentMethology) : null}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Metodologia"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.methology && Boolean(formik.errors.methology)}
                                    helperText={formik.touched.methology && formik.errors.methology}
                                />
                            )}
                            onChange={(event, newInputValue: MetodologiaResponse | null) => {
                                setCurrentMethology(undefined)
                                if (newInputValue) 
                                    setCurrentMethology(newInputValue.idservmetodologia)
                                handleOtherFieldChange(newInputValue,"methology")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservmetodologia === value?.idservmetodologia}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="sample"
                            options={optionsSample}
                            getOptionLabel={(option) => option.nombre}
                            value={currentSample !== undefined ? optionsSample.find((option) => option.idservmuestra === currentSample) : null}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Muestra"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.sample && Boolean(formik.errors.sample)}
                                    helperText={formik.touched.sample && formik.errors.sample}
                                />
                            )}
                            onChange={(event, newInputValue: MuestraResponse | null) => {
                                setCurrentSample(undefined)
                                if (newInputValue)
                                    setCurrentSample(newInputValue.idservmuestra)
                                handleOtherFieldChange(newInputValue,"sample")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservmuestra === value?.idservmuestra}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="param"
                            options={optionsParams}
                            getOptionLabel={(option) => option.nombre}
                            value={currentParam !== undefined ? optionsParams.find((option) => option.idservparametroservicio === currentParam) : null}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Parametros"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.param && Boolean(formik.errors.param)}
                                    helperText={formik.touched.param && formik.errors.param}
                                />
                            )}
                            onChange={(event, newInputValue: ParametrosResponse | null) => {
                                setCurrentParam(undefined)
                                if(newInputValue)
                                    setCurrentParam(newInputValue.idservparametroservicio)
                                handleOtherFieldChange(newInputValue,"param")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservparametroservicio === value?.idservparametroservicio}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            px: 1,
                        }}
                    >
                        <FormGroup>
                            <FormControlLabel
                                checked={isSampling}
                                control={<Switch />}
                                label="Requiere muestreo por el laboratorio"
                                onChange={(event, newInputValue: boolean) => {
                                    setIsSampling(newInputValue)
                                }}
                            />
                        </FormGroup>
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            px: 1
                        }}
                    >
                        <FormGroup>
                            <FormControlLabel
                                checked={isAcreditado}
                                control={<Switch />}
                                label="Requiere que el laboratorio este acreditado"
                                onChange={(event, newInputValue: boolean) => {
                                    setIsAcreditado(newInputValue)
                                }}
                            />
                        </FormGroup>
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <TextField
                            id="description"
                            multiline
                            rows={2}
                            label="Descripcin"
                            color="primary"
                            value={formik.values.description}
                            onChange={HandleTextFieldChange}
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.description && Boolean(formik.errors.description)}
                            helperText={formik.touched.description && formik.errors.description}
                            variant="outlined"
                            fullWidth
                            InputProps={{
                                sx: {
                                  borderRadius: '5px', // Aplica el radio de borde al TextField
                                  '& fieldset': {
                                    borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                  },
                                },
                            }}
                            InputLabelProps={{
                                sx: {
                                  color: themePalette.PRIMARY, // Cambia el color del label a azul
                                },
                            }}
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="laboratory"
                            options={optionsLaboratories}
                            getOptionLabel={(option) => `${option.laboratorio.nombre}`}
                            value={
                                currentLaboratory 
                                    ? optionsLaboratories.find( laboratory => 
                                        laboratory.laboratorio.idlablaboratorios === currentLaboratory?.laboratorio.idlablaboratorios) 
                                    : null
                                }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Selecione laboratorio a derivar"
                                />
                            )}
                            onChange={(event, newInputValue: GetLaboratoriesResponse | null) => {
                                if(newInputValue)
                                    setCurrentLaboratory(newInputValue)
                                else 
                                    setCurrentLaboratory(undefined)
                            }}
                            isOptionEqualToValue={(option, value) => option.laboratorio.idlablaboratorios === value?.laboratorio.idlablaboratorios}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                >
                    <Box
                        sx={{
                            width:"100%",
                            px: 1,
                        // mt: isSmallScreen ? 2 : 2,
                        }}
                    >
                        <Button 
                            type="submit"
                            variant="contained"
                            color="primary"
                            sx={{ 
                                mt: 2,
                                height: "60px",
                                width: '50%'
                            }}
                        >
                            Guardar
                        </Button>
                    </Box>
                </Grid>
            </Grid>
        </form>
    )
}

export default EditRequestFormAS