/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from 'react'
import { Avatar, Button, Card, CardContent,  Chip,  Container,  Grid, List, ListItem, ListItemAvatar, ListItemText, Typography } from '@mui/material'
import useRequestApi, { MetodoHTTP } from '../../hooks/useRequestApi'
import { ResponseFormat } from '../../types/API/Index'
import { UsersInfoRequestApi, UsersInfoResponseApi } from '../../types/API/User'
import BadgeIcon from '@mui/icons-material/Badge'
import PersonIcon from '@mui/icons-material/Person'
import PhoneIcon from '@mui/icons-material/Phone'
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail'
import DescriptionIcon from '@mui/icons-material/Description'
import HomeRepairServiceIcon from '@mui/icons-material/HomeRepairService'
import AlignVerticalCenterIcon from '@mui/icons-material/AlignVerticalCenter'
import DoneOutlineIcon from '@mui/icons-material/DoneOutline'
import BiotechIcon from '@mui/icons-material/Biotech'
import { useParams } from 'react-router-dom'
import { ChangeRequestStatusRequestApi, ChangeRequestStatusResponseApi, Estado, GetAllStatusResponseApi, GetRequestApi, Request } from '../../types/API/Request'
import { themePalette } from '../../config/MaterialUi/Theme.Object'
import { AuthContext } from '../../context/AuthContext'
import { UserData } from '../../types/DataUser'
import { Toastification } from '../../context/ToastificationContext'

const ViewRequest: React.FC = () => {

    const [title, setTitle] = useState<string>('')
    const [currentRequest, setCurrentRequest] = useState<Request>()
    const [statusOptions, setStatusOptions] = useState<Estado[]>()
    const [currentClientInfo, setCurrentClientInfo] = useState<any>() 

    const { id } = useParams()

    const requestGetInfoUser = useRequestApi<ResponseFormat<UsersInfoResponseApi>,UsersInfoRequestApi>({
        path: '/Auth/UsersInfo/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetRequest = useRequestApi<ResponseFormat<GetRequestApi>,string>({
        path: `/Solicitud/GetRequest/${id}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const requestChangeRequestStatus = useRequestApi<ResponseFormat<ChangeRequestStatusResponseApi>,ChangeRequestStatusRequestApi>({
        path: `/Solicitud/ChangeRequestStatus`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetAllStatus = useRequestApi<ResponseFormat<GetAllStatusResponseApi>,string>({
        path: `/Solicitud/GetAllStatus/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const { 
        userData
    } =  useContext(AuthContext)

    
    const {
        updateNotificationOpen,
        updateTypeNotification,
        updateMessage
    } = useContext(Toastification)

    const getColor = () => {
        switch (currentRequest?.estadoId??0) {
            case 1:
                return 'default'
            case 2: 
                return 'primary'
            case 3:
                return 'warning'
            case 4:
                return 'default'
            case 5:
                return 'success'
            default:
                return 'default'
        }
        
    }

    const updateStateSatatusRequest = async (status: number, compareValue: number) => {
        if (currentRequest?.estadoId === compareValue) {
            return await requestChangeRequestStatus.submit({
                estadoId: status,
                solicitudId: currentRequest.idsolsolicitud,
                laboratorioId: currentRequest?.laboratorioAsignadoId,
                usuarioDepartamentoId: (userData as UserData).useraccountid
            })
        }
       
    }

    const handleClickAccept = async () => {
        const response = await updateStateSatatusRequest(5,4)

        if (!response) 
            return 

        const { succeeded, message } = response

        if (succeeded){
            updateMessage(`${message} y aceptada`)
            updateNotificationOpen(true)
            setTimeout(() => {
                updateTypeNotification(true)
                updateNotificationOpen(false)
                window.location.href = '/Asistente-Laboratorio/solicitudes/'
            } ,1000)
        } else {
            updateMessage('Error no se pudo realizar la funcin')
            updateTypeNotification(false)
            updateNotificationOpen(true)
        }
    }

    const handleClickDecline = async () => {
        const response = await updateStateSatatusRequest(6,4)

        if (!response) 
            return 

        const { succeeded, message } = response

        if (succeeded){
            updateMessage(`${message} y rechazada`)
            updateNotificationOpen(true)
            setTimeout(() => {
                updateTypeNotification(true)
                updateNotificationOpen(false)
                window.location.href = '/Asistente-Laboratorio/solicitudes/'
            } ,1000)
        } else {
            updateMessage('Error no se pudo realizar la funcin')
            updateTypeNotification(false)
            updateNotificationOpen(true)
        }
    }

    useEffect(() => {
        const getAsyncAllState = async () => {
            console.log('entre')
            const response = await requestGetAllStatus.submit('')
            console.log('request: ',response )
            if(!response)
                return
            
            const { data } = response

            if (!data)
                return
            
            setStatusOptions(data)
        }
        getAsyncAllState()
    }, [currentRequest])


    // useEffect(() => {
    //     const asyncChangeSatusRequest = async () => {
    //         if (currentRequest?.estadoId === 3) {
    //             console.log('entre')
    //             await requestChangeRequestStatus.submit({
    //                 estadoId: 4,
    //                 solicitudId: currentRequest.idsolsolicitud,
    //                 laboratorioId: currentRequest.laboratorioAsignadoId,
    //                 usuarioDepartamentoId: (userData as UserData).useraccountid
    //             })
    //         }
    //     }

    //     asyncChangeSatusRequest()
    // }, [currentRequest])

    useEffect(() => {
        const asyncChangeSatusRequest = async () => {
            if (currentRequest?.estadoId === 3) {
                console.log('entre')
                await requestChangeRequestStatus.submit({
                    estadoId: 4,
                    solicitudId: currentRequest.idsolsolicitud,
                    laboratorioId: currentRequest.laboratorioAsignadoId,
                    usuarioDepartamentoId: (userData as UserData).useraccountid
                })
                setCurrentRequest( cr => cr ? { ...cr, estadoId: 4 } : cr)
            }
        }
        const asyncRequestClientInfo = async () => {
            console.log('currentRequest: ', currentRequest?.estadoId)
            if(currentRequest){ 
                const payloadInfoClient:UsersInfoRequestApi = [
                    currentRequest.clienteId
                ]

                const response = await requestGetInfoUser.submit(payloadInfoClient)
            
                
                if (!response)
                    return

                console.log('userInfo:' , response)
                setCurrentClientInfo(response)
                const { data } = response
                
                if (!data)
                    return
                setCurrentClientInfo(data)   
            }
        }

        if(currentRequest){
            asyncChangeSatusRequest()
            asyncRequestClientInfo()
        }
    }, [currentRequest])

    useEffect(() => {
        const asynGetRequest = async () => {
            const response = await requestGetRequest.submit('')

            if (!response)
            return
            console.log('response: ', response)
            const { data } = response
            
            if( !data )
                return
            setCurrentRequest(data)
        }

        asynGetRequest()
    }, [title])

    useEffect(() => {
        if( id )
            setTitle('Informacin de la solicitud')
    }, [id])
    
    return (
        <Container maxWidth="xl" >
            <Grid container spacing={2}>
                <Grid
                    item
                    xs={12}
                    md={5}
                >
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2 }} >
                        <CardContent>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="h4"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                Informacin del cliente
                            </Typography>
                            <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <PersonIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].nombre} ${currentClientInfo&&currentClientInfo[0].apellido}`} secondary="Nombre" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <BadgeIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={` ${currentClientInfo&&currentClientInfo[0].identificacion}`} secondary="Cdula" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <PhoneIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].telefono}`} secondary="Telefono" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <AlternateEmailIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].correo}`} secondary="Correo" />
                                </ListItem>
                            </List>
                        </CardContent>
                    </Card>
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2, mt: 2}} >
                        <CardContent>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="h4"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                Estado de la solicitud
                            </Typography>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="body1"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                <Chip
                                    color={getColor()}
                                    label={(statusOptions?.find( statu => statu.idsolestado === currentRequest?.estadoId))?.nombre ?? '' }
                                />
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid
                    item
                    xs={12}
                    md={7}
                >
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2 }} >
                        <CardContent>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="h4"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                Informacin de la solicitud
                            </Typography>
                            <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <DescriptionIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.descripcion} secondary="Descripcin" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <HomeRepairServiceIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.servicio} secondary="Servicio" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <BiotechIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.muestra} secondary="Muestra" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <BiotechIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.metodologia} secondary="Metodologia" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <AlignVerticalCenterIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.parametro} secondary="Parametro" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <DoneOutlineIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={currentRequest?.muestreo ? 'Si' : 'No'} secondary="Requiere Muestre" />
                                </ListItem>
                            </List>
                            <Button
                                variant='contained'
                                sx={{
                                    width:"100%",
                                    px: 1,
                                }}
                                onClick={handleClickAccept}
                            >
                                Aceptar Solicitud
                            </Button>
                            <Button
                                color='error'
                                variant='contained'
                                sx={{
                                    width:"100%",
                                    px: 1,
                                    mt: 1
                                }}
                                onClick={handleClickDecline}
                            >
                                Rechazar Solicitud
                            </Button>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    )
}

export default ViewRequest