import React, { ChangeEvent } from 'react'
import { useFormik } from 'formik';
import * as yup from 'yup'
import { DataForm } from '../types/Form';

type UseFormValidateHook = {
    initialValues: DataForm,
    validationSchema: yup.ObjectSchema<any, yup.AnyObject, any, "">
    onSubmit: (data: DataForm) => void
}

const useFormValidateHook = ({initialValues,validationSchema,onSubmit}: UseFormValidateHook) => {

    const formik = useFormik({
        initialValues,
        validationSchema,
        onSubmit
    })
    
    const HandleTextFieldChange = (event: ChangeEvent<HTMLInputElement>) => {
        formik.handleChange(event);
        formik.setFieldTouched(event.target.name, true, false);
    };
    
    const handleTextFieldBlur = (event: React.FocusEvent<HTMLInputElement>) => {
        formik.handleBlur(event);
    };

    const handleOtherFieldChange = (newValue: DataForm[keyof DataForm], name: string) => {
        formik.setFieldValue(name,newValue);
        formik.setFieldTouched(name, true, false);
    }

    const handleFileChange = (newValue: File | undefined, name: string) => handleOtherFieldChange(newValue, name)

    return {
        HandleTextFieldChange,
        handleTextFieldBlur,
        handleOtherFieldChange,
        handleFileChange,
        formik
    }
}

export default useFormValidateHook