export const formatDate = (date: string) => {
    const dateAux = new Date(date)
    const currentDate = new Date()
    const timeDifference = currentDate.getTime() - dateAux.getTime()

    const dayDifference = Math.floor(timeDifference/(1000 * 60 * 60 * 24))
    const hourDifference = Math.floor(timeDifference % (1000 * 60 * 60 * 24) / (1000 * 60 * 60))
    const minutesDifference = Math.floor((timeDifference % (1000 * 60 * 60)) / (1000 * 60))

    const newFormatDate = dateAux.toLocaleDateString('es-EC',{ year: 'numeric', month: '2-digit', day: '2-digit', timeZone: 'America/Guayaquil' })

    return `${newFormatDate} ${dayDifference}d ${hourDifference}h ${minutesDifference}m` 
}