import { useEffect, useState } from "react"
import { Box, Button, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent } from "@mui/material"
import AddIcon from '@mui/icons-material/Add'
import DataTable from "../../components/DataTable/DataTable"
import { ServicesColumnsTable } from "../../constants/DataTableAtributtesConstants"
import { Column, RowServices, TypesRowsDataTableContext, fetchDataFunc } from '../../types/DataTable';
import { Link, useNavigate } from "react-router-dom"
import { GetServicesRequestApi, GetServicesResponseApi } from "../../types/API/Services"
import useRequestApi, { MetodoHTTP } from "../../hooks/useRequestApi"
import { ResponseFormat } from "../../types/API/Index"
import DeleteService from "./components/DeleteService"

const Services: React.FC = () => {
    const [ columns, setColumns ] = useState<Column[]>([])
    const [showDelete, setShowDelete] = useState<boolean>(false)
    const [onlyActiveService, setOnlyActiveService] = useState(false)

    const navigate = useNavigate()

    const { submit } = useRequestApi<ResponseFormat<GetServicesResponseApi>,GetServicesRequestApi>({
        path: '/Servicio/GetServices/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const fetchData: fetchDataFunc<RowServices> = async (
        page = 1,
        rowsPerPage = 5,
        searchValue = '',
        sortColum = '',
        onlyActivate = onlyActiveService
    ) => {

        const payload: GetServicesRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: true,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            onlyActive: onlyActivate,
        }

        let totalRows = 0
        let rows: RowServices[] = []

        const response = await submit(payload)
        
        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowServices>(service => {
                    const { nombre, idservservicio, descripcion, deletedAt } = service.servicio
                    let row: RowServices = {
                        nombre,
                        descripcion,
                        id: idservservicio,
                        estado: deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                totalRows = totalCount
            }
        }

        return {
          data: rows,
          totalRows
        }
    }

    const handleModalDelete = () => {
        setShowDelete(true)
    }

    useEffect(() => {
      setColumns(ServicesColumnsTable)
    }, [])

    const handlePageEdit = (currentRowSelected: TypesRowsDataTableContext | undefined) => {
        const currentService: RowServices = currentRowSelected as RowServices
        navigate(`registrar/${currentService.id}`)
    }
  
    return (
        <Container maxWidth="xl">
            <Box
                sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between'
                }}
            >
                
                <DataTable<RowServices>
                    columns={columns}
                    fetchData={fetchData}
                    handlePageEdit={handlePageEdit}
                    setShowDelete={handleModalDelete}
                    estado={onlyActiveService}
                >
                    <FormControl sx={{ width: 200 }}>
                        <InputLabel id="estado">Estado</InputLabel>
                        <Select
                            labelId="estado"
                            id="estado"
                            value={onlyActiveService ? '1' : '0'}
                            label="Estado"
                            onChange={(event: SelectChangeEvent) => {
                                console.log('valor: ', event.target.value )
                                const value = (Number(event.target.value) === 1) ? true : false
                                console.log('estado: ', value)
                                setOnlyActiveService(value)
                            }}
                        >
                            <MenuItem value={1}>ACTIVOS</MenuItem>
                            <MenuItem value={0}>TODOS</MenuItem>
                        </Select>
                    </FormControl>
                    
                    <Link to='registrar' style={{ textDecoration: 'none' }}>
                        <Button 
                            sx={{ minHeight: '50px', mt: 1}}
                            variant="contained"
                            startIcon={<AddIcon />}
                        >
                            Agregar Servicio
                        </Button>
                    </Link>
                </DataTable>
            </Box>
            <DeleteService
                showDelete={showDelete}
                setShowDelete={setShowDelete}
            />
        </Container>
    )
}

export default Services
