import { createContext, useState } from "react";
import { TypesRowsDataTableContext } from "../types/DataTable";

export interface ContextDataTableProps<T> {
    currentRowSelected: T | undefined,
    setRow: (value: T) => void,
    setDataContext: (value: T[]) => void
    setDataTable: (setData: (value: T[]) => void) => void
    dataAux: T[]
}

const DataTableContext = createContext<ContextDataTableProps<TypesRowsDataTableContext>>({} as ContextDataTableProps<TypesRowsDataTableContext>)

function DataTableProvider({children}: any) {

    const [currentRowSelected, setcurrentRowSelected] = useState<TypesRowsDataTableContext|undefined>()
    const [dataAux, setDataAux] = useState<TypesRowsDataTableContext[]>([])

    function setRow(value: TypesRowsDataTableContext) {
        setcurrentRowSelected(value as TypesRowsDataTableContext)
    }

    function setDataContext(value: TypesRowsDataTableContext[]) {
        setDataAux(value as TypesRowsDataTableContext[])
        
    }

    function setDataTable(setData: (value: TypesRowsDataTableContext[]) => void) {
        setData(dataAux)
    }

    return (
        <DataTableContext.Provider
            value={{
                currentRowSelected,
                setDataContext,
                setRow,
                dataAux,
                setDataTable
            }}
        >
            { children }
        </DataTableContext.Provider>
    )
}

export { 
    DataTableProvider,
    DataTableContext
}