import { useContext, useEffect, useState } from 'react'
import { Autocomplete, Box, Button, Grid, TextField, useMediaQuery, useTheme } from '@mui/material'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { validationSchemaLaboratory } from '../../../constants/ValidationSchemas'
import { initialValueLaboratory } from '../../../constants/FormConstants'
import { DataForm, valueFormLaboratory } from "../../../types/Form"
import { GetServicesRequestApi, GetServicesResponseApi, Services } from '../../../types/API/Services'
import UploadFile from '../../../components/Form/UploadFileImage'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { CreateLaboratoryRequestApi, CreateLaboratoryResponseApi, GetLaboratoriesResponse, LaboratoryCreateRequest, LaboratoryUpdateRequest, UpdateLaboratoryRequestApi, UpdateLaboratoryResponseApi } from '../../../types/API/Laboratories'
import { DataTableContext } from '../../../context/DataTableContext'
import { RowLaboratories, RowServices } from '../../../types/DataTable'
import { Toastification } from '../../../context/ToastificationContext'
import useRequestImages from '../../../hooks/useRequestImages'
import { useParams } from 'react-router-dom'

const AddLaboratoryForm = () => {

    const [optionsServices, setOptionsServices] = useState<RowLaboratories[]>([])
    const [searchValueService, setSearchValueService] = useState<string| undefined>(undefined)
    const [src, setSrc] = useState<string>()

    const { id: idParams } = useParams()

    const theme = useTheme()
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('md')) 

    const { 
        currentRowSelected,
        setDataContext,
        setRow,
        dataAux
    } = useContext(DataTableContext)
    
    const {
        updateNotificationOpen,
        updateMessage
    } = useContext(Toastification)

    const useRequestApiCreate = useRequestApi<ResponseFormat<CreateLaboratoryResponseApi>,CreateLaboratoryRequestApi>({
        path: '/Laboratorio/CreateLaboratory',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestService = useRequestApi<ResponseFormat<GetServicesResponseApi>,GetServicesRequestApi>({
        path: '/Servicio/GetServices/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetLaboratory = useRequestApi<ResponseFormat<GetLaboratoriesResponse>,{id: string}>({
        path: `/Laboratorio/GetLaboratory/${idParams}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const useRequestApiUpdate = useRequestApi<ResponseFormat<UpdateLaboratoryResponseApi>,UpdateLaboratoryRequestApi>({
        path: `/Laboratorio/UpdateLaboratory/${idParams}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.PUT
    })

    const imagesRequest = useRequestImages<string,any>({
        path: 'Servicio/UploadServicePicture',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const imagesRequestGET = useRequestImages<File,string>({
        path: `Servicio/DownloadServicePicture/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const asyncRequestGetLaboratory = async () => {
        const response = await requestGetLaboratory.submit({id: idParams??''})
        if (!response)
            return
        
        const { data } = response

        if ( !data )
            return
        
        const { laboratorio, servicios } = data

        setSrc(laboratorio.imagen)

        const currentService = servicios.map<RowServices>( service => ({
            id: service.idservservicio,
            nombre: service.nombre,
            descripcion: '',
            estado: 'Activo'
        }))

        handleOtherFieldChange(currentService,"services")
        handleOtherFieldChange(laboratorio.nombre,"name")
        handleOtherFieldChange(laboratorio.descripcion,"description")        
    }

    useEffect(() => {
        const requestServices = async () => {
            const responseServices = await requestService.submit({
                search: searchValueService ?? '',
                pageNumber: 1,
                pageSize: 5,
                orderById: false,
                orderByName: false,
                orderByStatus: false,
                orderByDescription: false,
                onlyActive: false,
            })
            if ( responseServices ) {
                const { data } = responseServices
                if (!data)
                    return
                const { items } = data
                let rows: RowServices[] = items.map<RowServices>(service => {
                    const { nombre,idservservicio: id,descripcion,deletedAt } =  service.servicio
                    let row: RowServices = {
                        nombre,
                        descripcion,
                        id,
                        estado: deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                setOptionsServices(rows)
            }
        }
        idParams && asyncRequestGetLaboratory()
        requestServices()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchValueService])

    useEffect(() => {
        const requestAsyncImage = async () => {
            const fileImage = await imagesRequestGET.submit(src??'')
            if ( !fileImage )
                return
            //formik.values.image = fileImage
            const blob = new Blob([fileImage], { type: 'image/jpeg' })
            handleOtherFieldChange(blob,"image")
        }
        requestAsyncImage()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [src])

    useEffect(() => {
        setSearchValueService('')
        //if (currentRowSelected)
            
    }, [])
    

    const handleSubmit = async (data: DataForm) => {
        const dataFormAux = data as valueFormLaboratory
        const { description, name, services, image } = dataFormAux
        
        const formData = new FormData()
        if (!image)
            return 
        formData.append('file', image)
        let urlImage = await imagesRequest.submit(formData)  

        if(typeof urlImage === 'number')
            urlImage = src ?? ''
        
        if (!idParams){
            const laboratorio: LaboratoryCreateRequest = {
                nombre: name,
                descripcion: description,
                imagen: urlImage ?? ''
            }
    
            const payload: CreateLaboratoryRequestApi = {
                laboratorio,
                servicios: services.map( s => s.id)
            }
            const response = await useRequestApiCreate.submit(payload)
            if( !response )
                return 
            const { message, succeeded } = response
            if ( succeeded ){
                updateMessage(message)
                updateNotificationOpen(true)
                setTimeout(()=>{
                    window.location.href = '/Administrador/laboratorios'
                },1000)
            }
         } else{
            const laboratorio: LaboratoryUpdateRequest = {
                idlablaboratorios: Number(idParams),
                nombre: name,
                descripcion: description,
                imagen: urlImage ?? ''
            }
    
            const payload: UpdateLaboratoryRequestApi = {
                laboratorio: laboratorio,
                servicios: services.map( s => s.id)
            }
            const response = await useRequestApiUpdate.submit(payload)
            if( !response )
                return 
            const { message, succeeded } = response
            if ( succeeded ){
                updateMessage(message)
                updateNotificationOpen(true)
                setTimeout(()=>{
                    window.location.href = '/Administrador/laboratorios'
                },1000)
            }
            
        }
    }

    const {
        HandleTextFieldChange,
        handleTextFieldBlur,
        handleOtherFieldChange,
        handleFileChange,
        formik
    } = useFormValidateHook({
        initialValues: initialValueLaboratory,
        validationSchema: validationSchemaLaboratory,
        onSubmit: handleSubmit
    }) 

    return (
        <form onSubmit={formik.handleSubmit}>
            <Grid container spacing={2}>
                <Grid
                    item
                    xs={12}
                >
                    <Box
                        sx={{
                            display: 'flex',
                            flexDirection: isSmallScreen ? 'column' : 'row',
                        }}
                    >
                        <Box 
                            sx={{
                                width:"100%",
                                px: 1
                            }}
                        >
                            <TextField
                                id="name"
                                label="Nombre"
                                value={formik.values.name}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.name && Boolean(formik.errors.name)}
                                helperText={formik.touched.name && formik.errors.name}
                                variant="outlined"
                                fullWidth
                                color="primary"
                            />
                        </Box>
                        <Box 
                            sx={{
                                width: "100%",
                                mt: isSmallScreen ? 4 : '',
                                px: 1,
                            }} 
                        >
                            <Autocomplete
                                disablePortal
                                multiple
                                id="services"
                                options={optionsServices}
                                value={formik.values.services as RowServices[]}
                                getOptionLabel={(option) => option.nombre}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        variant="outlined"
                                        label="Servicios"
                                        onBlur={handleTextFieldBlur}
                                        error={formik.touched.services && Boolean(formik.errors.services)}
                                        helperText={formik.touched.services && formik.errors.services}
                                    />
                                )}
                                onInputChange={(event, newValue) => {
                                    setSearchValueService(newValue)
                                }}
                                onChange={(event, newInputValue: RowServices[]) => {
                                    handleOtherFieldChange(newInputValue,"services")
                                }}
                                isOptionEqualToValue={(option, value) => option.id === value.id}
                                fullWidth
                            />
                        </Box>
                    </Box>
                </Grid>
                <Grid
                    item
                    xs={12}
                    md={6}
                >
                            <Box 
                                sx={{
                                    width:"100%",
                                    px: 1
                                }}
                            >
                                <TextField
                                    id="description"
                                    multiline
                                    rows={10}
                                    label="Descripcin"
                                    value={formik.values.description}
                                    onChange={HandleTextFieldChange}
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.description && Boolean(formik.errors.description)}
                                    helperText={formik.touched.description && formik.errors.description}
                                    variant="outlined"
                                    fullWidth
                                    color="primary"
                                />
                            </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                >
                    <Box sx={{ width: "100%", display: "flex", flexDirection: "column",justifyContent: "space-between" }}>
                        <UploadFile
                            image={true}
                            handleValue={handleFileChange}
                            value={formik.values.image as File | undefined}
                            type="image/"
                            name="image"
                        />
                    </Box>
                </Grid>
            </Grid>
            <Button 
                type="submit"
                variant="contained"
                color="primary"
                sx={{ 
                    mt: 2,
                    height: "60px",
                    width: '50%'
                }}
            >
                Guardar
            </Button>
        </form>
    )
}

export default AddLaboratoryForm