import { Avatar, Box, Button, Card, CardContent, Grid, Link, TextField, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import { themePalette } from '../../../config/MaterialUi/Theme.Object'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { initialValueRegister } from '../../../constants/FormConstants'
import { DataForm, valueFormRegister } from '../../../types/Form';
import { validationSchemaRegister } from '../../../constants/ValidationSchemas'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { RegisterRequestApi, RegisterResponseApi } from '../../../types/API/Register'
import { useContext } from 'react'
import { Toastification } from '../../../context/ToastificationContext'


const RegisterForm = () => {

    const { submit } = useRequestApi<ResponseFormat<RegisterResponseApi>,RegisterRequestApi>({
        path: '/Auth/register/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const {
        updateNotificationOpen,
        updateMessage
    } = useContext(Toastification)

    const handleSubmit = async (dataForm: DataForm) => {
        const dataformAux = dataForm as valueFormRegister
        const { email, identification, lastName, name, password, telefono } = dataformAux
        const payload: RegisterRequestApi = {
            email,
            name,
            identification,
            lastName,
            password,
            telefono,
            rol: 'CL',
        }
        const response = await submit(payload)

        if ( !response ) 
            return
        const { message } = response
        updateMessage(message)
        updateNotificationOpen(true)

        formik.setValues(initialValueRegister)
        //console.log(data)
    }

    const {
        HandleTextFieldChange,
        handleTextFieldBlur,
        formik
    } = useFormValidateHook({
        initialValues: initialValueRegister,
        validationSchema: validationSchemaRegister,
        onSubmit: handleSubmit
    }) 
    
    return (
        <Card sx={{ borderWidth:'bold', borderRadius: 5, px: 1 }}>
        <CardContent>
            <Box
                sx={{
                my: 8,
                mx: 'auto',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: 'primary.main',  }}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5" color={themePalette.PRIMARY}>
                    Iniciar Sesion
                </Typography>
                <form onSubmit={formik.handleSubmit}>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="email"
                                label="Correo"
                                type='email'
                                value={formik.values.email}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.email && Boolean(formik.errors.email)}
                                helperText={formik.touched.email && formik.errors.email}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                            }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="name"
                                label="Nombres"
                                type='text'
                                value={formik.values.name}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.name && Boolean(formik.errors.name)}
                                helperText={formik.touched.name && formik.errors.name}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                            }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="lastName"
                                label="Apellidos"
                                type='text'
                                value={formik.values.lastName}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.lastName && Boolean(formik.errors.lastName)}
                                helperText={formik.touched.lastName && formik.errors.lastName}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                            }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="password"
                                label="Contrasea"
                                type='password'
                                value={formik.values.password}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.password && Boolean(formik.errors.password)}
                                helperText={formik.touched.password && formik.errors.password}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                                }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="repeatPassword"
                                label="Repetir contrasea"
                                type='password'
                                value={formik.values.repeatPassword}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.repeatPassword && Boolean(formik.errors.repeatPassword)}
                                helperText={formik.touched.repeatPassword && formik.errors.repeatPassword}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                                }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="telefono"
                                label="Telfono"
                                type='text'
                                value={formik.values.telefono}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.telefono && Boolean(formik.errors.telefono)}
                                helperText={formik.touched.telefono && formik.errors.telefono}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                            }}
                            />
                        </Box>
                        <Box 
                            sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                            }}
                        >
                            <TextField
                                id="identification"
                                label="Cdula"
                                type='text'
                                value={formik.values.identification}
                                onChange={HandleTextFieldChange}
                                onBlur={handleTextFieldBlur}
                                error={formik.touched.identification && Boolean(formik.errors.identification)}
                                helperText={formik.touched.identification && formik.errors.identification}
                                variant="outlined"
                                fullWidth
                                InputProps={{
                                    sx: {
                                        borderRadius: '10px', // Aplica el radio de borde al TextField
                                        '& fieldset': {
                                        borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                        },
                                    },
                                }}
                                InputLabelProps={{
                                    sx: {
                                        color: themePalette.PRIMARY, // Cambia el color del label a azul
                                    },
                            }}
                            />
                        </Box>
                        <Box
                        sx={{
                            minWidth: "400px",
                            mt: 1,
                            px: 1
                        }}
                        >
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 1, mb: 2 }}
                        >
                            Registrarse
                        </Button>
                        </Box>
                        <Box
                        sx={{
                            minWidth: "400px",
                            mt: 2,
                            px: 1
                        }}
                        >
                        <Grid container>
                            <Grid item>
                                <Link href="/Login/autenticar" variant="body2">
                                    {"Iniciar Sesin."}
                                </Link>
                            </Grid>
                        </Grid>
                    </Box>
                </form>
            </Box>
        </CardContent>
        </Card>
    )
}

export default RegisterForm