import { Card, CardContent, Container, Typography } from "@mui/material"
import AddUserForm from "./components/AddUserForm"
import { themePalette } from "../../config/MaterialUi/Theme.Object"


const AddUser: React.FC<{}> = () => {
  return (
    <Container maxWidth="lg" >
      <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, boxShadow: 'none' }} >
        <CardContent>
        <Typography
            color={themePalette.PRIMARY}
            variant="h5"
            sx={{
              my: 2,
              mx: 1
            }}
        >
            Registrar Usuarios
        </Typography>
          <AddUserForm/>
        </CardContent>
      </Card>
    </Container>
  )
}

export default AddUser