import { useEffect, useState } from "react"
import { Box, Button, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent } from "@mui/material"
import AddIcon from '@mui/icons-material/Add'
import DataTable from "../../components/DataTable/DataTable"
import { Column, RowUsers, fetchDataFunc } from "../../types/DataTable"
import { UsersColumnsTable } from "../../constants/DataTableAtributtesConstants"
import { Link } from "react-router-dom"
import { ResponseFormat } from "../../types/API/Index"
import useRequestApi, { MetodoHTTP } from "../../hooks/useRequestApi"
import { GetUsersRequestApi, GetUsersResponseApi } from "../../types/API/User"
import DeleteUser from "./components/DeleteUser"

const Users = () => {
    const [ columns, setColumns ] = useState<Column[]>([])
    const [showDelete, setShowDelete] = useState<boolean>(false)
    const [onlyActiveService, setOnlyActiveService] = useState(false)

    const { submit } = useRequestApi<ResponseFormat<GetUsersResponseApi>,GetUsersRequestApi>({
        path: '/Auth/GetUsers/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const fetchData: fetchDataFunc<RowUsers> = async (
        page = 1,
        rowsPerPage = 5,
        searchValue = '',
        sortColum = '',
        onlyActivate = onlyActiveService
    ) => {
        const payload: GetUsersRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: false,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            onlyActive: onlyActivate,
        }

        let totalRows = 0
        let rows: RowUsers[] = []

        const response = await submit(payload)

        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowUsers>(user => {
                    const { nombre, deletedAt, apellido, idusrusuario, rol } = user
                    let row: RowUsers = {
                        nombre: `${nombre} ${apellido}`,
                        estado: deletedAt ? 'INACTIVO' : 'ACTIVO',
                        id: idusrusuario,
                        rol
                    }
                    return row
                })
                totalRows = totalCount
            }
        }

        return {
          data: rows,
          totalRows
        }
    }
    
    const handleModalDelete = () => {
        setShowDelete(true)
    }

    useEffect(() => {
        setColumns(UsersColumnsTable)
    }, [])

    return (
        <Container maxWidth="xl">
            <Box
                sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between'
                }}
            >
                
                <DataTable<RowUsers>
                    columns={columns}
                    fetchData={fetchData}
                    showEditOption={false}
                    setShowDelete={handleModalDelete}
                    estado={onlyActiveService}
                >
                    <FormControl sx={{ width: 200 }}>
                        <InputLabel id="estado">Estado</InputLabel>
                        <Select
                            labelId="estado"
                            id="estado"
                            value={onlyActiveService ? '1' : '0'}
                            label="Estado"
                            onChange={(event: SelectChangeEvent) => {
                                console.log('valor: ', event.target.value )
                                const value = (Number(event.target.value) === 1) ? true : false
                                console.log('estado: ', value)
                                setOnlyActiveService(value)
                            }}
                        >
                            <MenuItem value={1}>ACTIVOS</MenuItem>
                            <MenuItem value={0}>TODOS</MenuItem>
                        </Select>
                    </FormControl>
                    <Link to='registro' style={{ textDecoration: 'none' }}>
                        <Button 
                            sx={{ minHeight: '50px', mt: 1}}
                            variant="contained"
                            startIcon={<AddIcon />}
                        >
                            Aadir usuario
                        </Button>
                    </Link>
                </DataTable>
            </Box>
            <DeleteUser
                showDelete={showDelete}
                setShowDelete={setShowDelete}
            />
        </Container>
    )
}

export default Users