import { RouteObject } from 'react-router-dom'
import Error from '../../pages/Error'
import SilabAssistant from '../../pages/SilabAssistant/Index'
import RequestSilab from '../../pages/SilabAssistant/Request'
import EditRequest from '../../pages/SilabAssistant/EditRequest'

const SilabAssistantRouter: RouteObject[] = [
    {
      path: "/Asistente-Silab",
      element: <SilabAssistant/>,
      errorElement: <Error/>,
      children: [
        {
            path: "solicitudes",      
            element: <RequestSilab/>,
            errorElement: <Error/>
        },
        {
          path: "solicitudes/editar/:id",      
          element: <EditRequest/>,
          errorElement: <Error/>
        },
      ]
    }
]

export { SilabAssistantRouter }