import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Card, CardContent, Container, Typography } from '@mui/material'
import { themePalette } from '../../config/MaterialUi/Theme.Object'
import useRequestApi, { MetodoHTTP } from '../../hooks/useRequestApi'
import { ResponseFormat } from '../../types/API/Index'
import { GetStandardServiceResponseApi } from '../../types/API/ServiceStandardization'
import AddStandardizationServiceForm from './components/AddStandardizationServiceForm'

const AddServiceStandardizations:React.FC = () => {

    const [title, setTitle] = useState<string>('')
    const [currentStandardizationService, setCurrentStandardizationService] = useState<GetStandardServiceResponseApi>()

    const { id } = useParams()

    const requestGetServiceStandardization = useRequestApi<ResponseFormat<GetStandardServiceResponseApi>,string>({
        path: `/ServicioEstandarizado/GetStandardService/${id}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    useEffect(() => {
        const asyncGetRequest = async () => {
            const response = await requestGetServiceStandardization.submit('')
    
            if (!response)
                return
    
            const { data } = response
            
            if( !data )
                return
        
            setCurrentStandardizationService(data)
        }
  
        if (id)
            asyncGetRequest()
    }, [title])

    useEffect(() => {
        if ( id )
          setTitle('Edtar')
        else
          setTitle('Crear')  
    }, [id])

    return (
        <Container maxWidth="lg">
            <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2 }} >
                <CardContent>
                <Typography
                    color={themePalette.PRIMARY}
                    variant="h4"
                    sx={{
                        my: 2,
                        mx: 1
                    }}
                    >
                    { title } Servicio Estandarizado
                    </Typography>
                <AddStandardizationServiceForm
                    currentStandardizationService={currentStandardizationService}
                    idStandardizationService={id}
                />   
                </CardContent>
            </Card>
        </Container>
    )
}

export default AddServiceStandardizations