import { useContext, useEffect } from 'react'
import { DataForm, valueFormSample } from '../../../types/Form';
import { initialValueSample } from '../../../constants/FormConstants'
import { Box, Button, TextField } from '@mui/material'
import { validationSchemaSample } from '../../../constants/ValidationSchemas'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { DataTableContext } from '../../../context/DataTableContext'
import { RowMuestra } from '../../../types/DataTable'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { CreateSampleRequestApi, CreateSampleResponseApi, UpdateSampleRequestApi, UpdateSampleResponseApi } from '../../../types/API/Sample'
import { Toastification } from '../../../context/ToastificationContext';


const AddSampleForm:  React.FC = () => {
  
  const {
    updateNotificationOpen,
    updateMessage,
    updateTypeNotification
  } = useContext(Toastification)
  
  const { 
    currentRowSelected,
    setRow,
    setDataContext,
    dataAux
  } = useContext(DataTableContext)

  const useRequestApiCreate = useRequestApi<ResponseFormat<CreateSampleResponseApi>,CreateSampleRequestApi>({
    path: '/Recurso/CreateSample',
    url: process.env.REACT_APP_API_GATEWAY,
    method: MetodoHTTP.POST
  })

  const useRequestApiUpdate = useRequestApi<ResponseFormat<UpdateSampleResponseApi>,UpdateSampleRequestApi>({
    path: `/Recurso/UpdateSample/${(currentRowSelected as RowMuestra)?.id}`,
    url: process.env.REACT_APP_API_GATEWAY,
    method: MetodoHTTP.PUT
  })

  const handleSubmit = async (dataForm: DataForm) => {
    const dataFormAux = dataForm as valueFormSample
    if ( currentRowSelected ){
      onEdit(dataFormAux)
      return
    }

    const payload: CreateSampleRequestApi = {
      nombre: dataFormAux.name,
      descripcion: dataFormAux.description
    }

    const response = await useRequestApiCreate.submit(payload)
    if( !response )
      return
    const { succeeded, message } = response

    if ( succeeded ){
      updateMessage(message)
      updateNotificationOpen(true)
      setDataContext([
        {
          nombre: dataFormAux.name,
          descripcion: dataFormAux.description
        },
        ...dataAux,
      ])
    } else {
      updateMessage(message)
      updateTypeNotification(false)
      updateNotificationOpen(true)
    }

    setTimeout(() => {
      updateTypeNotification(true)
      updateNotificationOpen(false)
    } ,3000)
  }

  const onEdit = async (dataForm: valueFormSample) => {
    const currentRow = currentRowSelected as RowMuestra
    const response = await useRequestApiUpdate.submit({
      nombre: dataForm.name,
      descripcion: dataForm.description,
      idservmuestra: currentRow.id
    })
    if (!response)
      return
    const { succeeded, message } = response
    if(succeeded){
      const data = (dataAux as RowMuestra[]).map( sample => {
        let auxMethology: RowMuestra = sample
        if ( sample.id === currentRow.id ) {
          auxMethology.nombre = dataForm.name
          auxMethology.descripcion = dataForm.description
        }
        return auxMethology
      })
      updateMessage(message)
      updateNotificationOpen(true)
      setDataContext(data)
      setRow(undefined)
    } else {
      updateMessage(message)
      updateTypeNotification(false)
      updateNotificationOpen(true)
    }

    setTimeout(() => {
      updateTypeNotification(true)
      updateNotificationOpen(false)
    } ,3000)
  }

  const {
    HandleTextFieldChange,
    handleTextFieldBlur,
    handleOtherFieldChange,
    formik
  } = useFormValidateHook({
    initialValues: initialValueSample,
    validationSchema: validationSchemaSample,
    onSubmit: handleSubmit
  })  

  useEffect(() => {
    if ( !currentRowSelected )
      return
    const { nombre, descripcion } = currentRowSelected as RowMuestra
    handleOtherFieldChange(nombre,"name")
    handleOtherFieldChange(descripcion,"description")
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentRowSelected])

  return (
    <div>
      <h1>Registrar Muestra</h1>
        <form onSubmit={formik.handleSubmit}>
          <Box>
            <TextField
                id="name"
                name='name'
                label="Nombre"
                value={formik.values.name}
                onChange={HandleTextFieldChange}
                onBlur={handleTextFieldBlur}
                error={formik.touched.name && Boolean(formik.errors.name)}
                helperText={formik.touched.name && formik.errors.name}
                variant="outlined"
                fullWidth
                color="primary"
            />
          </Box>
          <Box sx={{ mt: 2 }}>
            <TextField
              id="description"
              name="description"
              multiline
              rows={3}
              label="Descripcin"
              value={formik.values.description}
              onChange={HandleTextFieldChange}
              onBlur={handleTextFieldBlur}
              error={formik.touched.description && Boolean(formik.errors.description)}
              helperText={formik.touched.description && formik.errors.description}
              variant="outlined"
              fullWidth
              color="primary"
            />
          </Box>
          <Box  sx={{ mt: 2, width: '100%' }}>
            <Button
              type='submit'
              variant='contained'
            >
              Guardar
            </Button>
          </Box>
        </form>
    </div>
  )
}

export default AddSampleForm
