import { RouteObject, } from 'react-router-dom'
import Index from '../../pages/Login/Index'
import Error from '../../pages/Error'
import LoginForm from '../../pages/Login/components/LoginForm'
import RegisterForm from '../../pages/Login/components/RegisterForm'

const loginRouter: RouteObject[] = [
    {
        path: "/Login",
        element: <Index/>,
        errorElement: <Error/>,
        children: [
            {
                path: "autenticar",
                element: <LoginForm key="autenticar"/>,
                errorElement: <Error/>,
            },
            {
                path: "registrar",
                element: <RegisterForm key="registrar"/>,
                errorElement: <Error/>,
            },
            {
                path: '*',
                element: <Error />,
            }
        ]
    }
]

export { loginRouter}