import { forwardRef, useContext, useEffect, useState } from 'react'
import Stack from '@mui/material/Stack'
import Snackbar from '@mui/material/Snackbar'
import MuiAlert, { AlertProps } from '@mui/material/Alert'
import { Toastification } from '../context/ToastificationContext'

const Alert = forwardRef<HTMLDivElement, AlertProps>(function Alert(
  props,
  ref,
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
})

const ToastificationContent: React.FC = () => {
    const [open, setOpen] = useState<boolean>(false);
    const [messageNotification, setMessageNotification] = useState<string>('');

    
    const {
        message,
        notificationOpen,
        typeNotification,
        updateTypeNotification
    } = useContext(Toastification)

    const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
        if (reason === 'clickaway') {
            return
        }

        setOpen(false)
        updateTypeNotification(true)
    }

    useEffect(() => {
        setOpen(notificationOpen)
        setMessageNotification(message)
    }, [notificationOpen,message])
    

    return (
        <Stack spacing={2} sx={{ width: '100%' }}>
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                anchorOrigin={{  vertical: 'top', horizontal: 'right' }}
            >
                <Alert onClose={handleClose} severity={ typeNotification ? 'success' : 'error'} sx={{ width: '100%' }}>
                    { messageNotification }
                </Alert>
            </Snackbar>
        </Stack>
    )
}

export default ToastificationContent