import { useContext, useEffect, useState } from "react"
import ModalAdd from "./ModalAdd"
import { Button, Typography } from '@mui/material'
import { DataTableContext } from "../../../context/DataTableContext"
import { RowMetodologia } from "../../../types/DataTable"
import useRequestApi, { MetodoHTTP } from "../../../hooks/useRequestApi"
import { ResponseFormat } from "../../../types/API/Index"
import { DeleteMetodologyRequestApi, DeleteMetodologyResponseApi } from "../../../types/API/Methology"
import { Toastification } from "../../../context/ToastificationContext"

type DeleteMethologyProps = {
    showDelete: boolean,
    setShowDelete: (value: boolean) => void
}

const DeleteMethology: React.FC<DeleteMethologyProps> = ({ showDelete, setShowDelete }) => {
    const [title, setTitle] = useState<string>('Desactivar')
    const [isDelete, seIsDelete] = useState<boolean>(true)

    const { 
        currentRowSelected,
        setDataContext,
        dataAux
    } = useContext(DataTableContext)

    const {
        updateNotificationOpen,
        updateMessage
    } = useContext(Toastification)

    useEffect(() => {
        const currentRow = currentRowSelected as RowMetodologia
        if (currentRow?.estado === 'INACTIVO') {
            setTitle('Activar')
            seIsDelete(false)
        } else{
            setTitle('Inactivar')
            seIsDelete(true)
        }
    }, [currentRowSelected])

    const { submit } = useRequestApi<ResponseFormat<DeleteMetodologyResponseApi>,DeleteMetodologyRequestApi>({
        path: `/Recurso/DeleteMetodology/${(currentRowSelected as RowMetodologia)?.id}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const handleDelete = async () => {
        const currentRow = currentRowSelected as RowMetodologia
        const response = await submit({
            id: currentRow.id,
            delete: isDelete
        })

        if (!response)
            return
        const { succeeded, message  } = response
        
        if (!succeeded)
            return
        const data = (dataAux as RowMetodologia[]).filter( methology => {
            let auxMethology: RowMetodologia = methology
            return auxMethology.id !== currentRow.id
        })
        setDataContext(data)
        updateMessage(message)
        updateNotificationOpen(true)
    }
  
    return (
        <ModalAdd
            show={showDelete}
            setShow={setShowDelete}
        >
            <h1>{title} Metodologia</h1>
            <Typography
                variant='body1'
            >
                Esta seguro de {title} esta Metodologia
            </Typography>
            <Button
                type='button'
                variant='contained'
                color={isDelete ? 'error' : 'primary'}
                onClick={handleDelete}
            >
                {title}
            </Button>
        </ModalAdd>
    )
}

export default DeleteMethology