import { RouteObject } from 'react-router-dom'
import Client from '../../pages/Client/Index'
import Request from '../../pages/Client/Request'
import Error from '../../pages/Error'
import AddRequest from '../../pages/Client/AddRequest'


const clientRouter: RouteObject[] = [
    {
      path: "/Cliente",
      element: <Client/>,
      errorElement: <Error/>,
      children: [
        {
          path: "mis-solicitudes",      
          element: <Request/>,
          errorElement: <Error/>
        },
        {
          path: "mis-solicitudes/registrar",      
          element: <AddRequest/>,
          errorElement: <Error/>
        },
        {
          path: "mis-solicitudes/registrar/:id",      
          element: <AddRequest/>,
          errorElement: <Error/>
        },
      ]
    }
]

export { clientRouter}