/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState, MouseEvent, ChangeEvent, useContext } from 'react';
import { useMediaQuery, useTheme } from "@mui/material"
import { TypesRowsDataTableContext, fetchDataFunc } from "../types/DataTable"
import { DataTableContext } from "../context/DataTableContext"

type UseDataTableProps<T> = {
    fetchData: fetchDataFunc<T> | null,
    request: boolean
}

function useDataTableHook<T>({fetchData, request}: UseDataTableProps<T>) {
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState<any[]>([])
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(5)
    const [totalRows, setTotalRows] = useState(0)
    const [searchValue, setSearchValue] = useState('')
    const [sortColumn, setSortColumn] = useState('')
    const [onlyActive, setOnlyActive] = useState(false)
    const [statusId, setStatusId] = useState(0)
  
    const theme = useTheme()
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'))

    const { setDataContext, setDataTable, dataAux } = useContext(DataTableContext)
  
    useEffect(() => {
      setIsLoading(true)
      setDataTable(setData)
      const fetchDataAsync = async () => {
        if (fetchData){
          const result = await fetchData(page+1, rowsPerPage, searchValue, sortColumn,onlyActive,statusId)
          console.log('data: ', result)
          setTimeout(() => {
            setData(result.data)
            setDataContext(result.data as TypesRowsDataTableContext[])
            setTotalRows(result.totalRows)
            setIsLoading(false)
          },800)
        } 
      }
      
      if (request) {
        fetchDataAsync()
      }
    }, [page, rowsPerPage, searchValue, sortColumn, fetchData, request, onlyActive, statusId])

  
    const handleChangePage = (event: MouseEvent<HTMLButtonElement> | null, newPage: number) => {
      console.log('Page: ', newPage)
      setPage(newPage)
    }
  
    const handleChangeRowsPerPage = (event: ChangeEvent<HTMLInputElement>) => {
      setRowsPerPage(parseInt(event.target.value, 10))
      setPage(0)
    }
  
    const handleSearchChange = (event: ChangeEvent<HTMLInputElement>) => {
      setSearchValue(event.target.value)
      setPage(0)
    }
  
    const handleSortColumn = (columnId: string) => {
      console.log('sort')
      if (sortColumn === columnId) {
        setSortColumn(`-${columnId}`)
      } else {
        setSortColumn(columnId)
      }
      setPage(0)
    }

    return {
        isSmallScreen,
        searchValue,
        handleSortColumn,
        handleChangeRowsPerPage,
        handleSearchChange,
        handleChangePage,
        totalRows,
        rowsPerPage,
        data,
        page,
        isLoading,
        setOnlyActive,
        setStatusId
    }
}

export default useDataTableHook