import { useEffect, useState } from "react"
import { reqResApi } from "../config/api/Request.Config"

export enum MetodoHTTP {
    GET = "GET",
    POST = "POST",
    PUT = "PUT",
    DELETE = "DELETE",
    PATCH = "PATCH",
    HEAD = "HEAD",
    OPTIONS = "OPTIONS",
  }
  

type UseRequestApi<T,K> = {
    path: string
    url: string | undefined,
    method: MetodoHTTP
}

const headers = {
    'Content-Type': 'application/json',
}

function useRequestApi<T,K>({path, url, method}: UseRequestApi<T,K>) {

    const [apiUrl, setApiUrl] = useState<string>()

    useEffect(() => {
        setApiUrl(url)
    }, [url,path])

    const getRequest = async (payload: any) => {
        if (!apiUrl)
            return
        console.log('Api Url: ', apiUrl+path)
        const res = await reqResApi(apiUrl).get<T>(path,{
            params: {
                id: payload?.id??''
            },
            maxRedirects: 5 
        } )
        console.log('respuesta:', res)
        return res.data
    }

    const postRequest = async (payload: K) => {
        if (!apiUrl)
            return
        const res = await reqResApi(apiUrl).post<T>(path, 
            payload,
            {
                headers
            }    
        )
        return res.data
    }
    
    const putRequest = async (payload: K) => {
        if (!apiUrl)
            return
        const res = await reqResApi(apiUrl).put<T>(path, 
            payload,
            {
                headers
            }    
        )
        return res.data
    }
    
    const submit = async (payload: K) => {
        if (!apiUrl)
            return

        switch (method) {
            case MetodoHTTP.POST:
                return postRequest(payload)
            case MetodoHTTP.PUT:
                return putRequest(payload)
            case MetodoHTTP.GET:
                console.log('llegue al Switch')
                return getRequest(payload)
            default:
                break;
        }
        
    }

    return {
        submit
    }
}

export default useRequestApi