import { IconButton } from "@mui/material"
import EditIcon from '@mui/icons-material/Edit'
import { useContext } from "react"
import { DataTableContext } from "../../context/DataTableContext"
import { TypesRowsDataTableContext } from "../../types/DataTable"

interface EditButtonProps {
    row: any,
    handleModalEdit?: () => void,
    handlePageEdit? : (currentRowSelected: TypesRowsDataTableContext | undefined) => void
}

const EditButton: React.FC<EditButtonProps> = ({ row, handleModalEdit, handlePageEdit}) => {

    const { setRow } = useContext(DataTableContext)

    const handleEdit = () => {
        setRow(row)
        if ( handleModalEdit )
            handleModalEdit()
            
        if ( handlePageEdit )
            handlePageEdit(row)

        
        console.log('Editar:' , row)
    }

    return (
    <IconButton onClick={handleEdit} color="secondary">
        <EditIcon />
    </IconButton>
    )
}

export default EditButton