import {useEffect} from 'react'
import { Skeleton, Stack, Typography } from '@mui/material'
import { 
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    TablePagination,
    TextField,
    Box,
    Card,
    CardContent,
    CircularProgress 
} from '@mui/material'
import DeleteButton from './DeleteButton'
import EditButton from './EditButton'
import { DataTableProps } from '../../types/DataTable'
import { themePalette } from '../../config/MaterialUi/Theme.Object'
import useDataTableHook from '../../hooks/useDataTableHook'


function DataTable<T>({ columns, fetchData, children, search = true, request = true, handleModalEdit, handlePageEdit, setShowDelete, showEditOption = true, showDeleteOption = true, estado = false, estadoId=0 }: DataTableProps<T>) {
  
  const {
    isSmallScreen,
    searchValue,
    handleSortColumn,
    handleChangeRowsPerPage,
    handleSearchChange,
    handleChangePage,
    totalRows,
    rowsPerPage,
    data,
    page,
    isLoading,
    setOnlyActive,
    setStatusId
  } = useDataTableHook<T>({fetchData,request})



  useEffect(() => {
    setOnlyActive(estado)
  }, [estado])

  useEffect(() => {
    setStatusId(estadoId)
  }, [estadoId])

  return (
    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2}} >
      <CardContent>
                <Box>
                  <Box 
                    sx={{ 
                      display: 'flex',
                      flexDirection: isSmallScreen ? 'column' : 'row', // Cambia la direccin de los elementos en columna para pantallas pequeas
                      alignItems: isSmallScreen ? 'center' : 'flex-start', // Centra los elementos en columna para pantallas pequeas
                      justifyContent: isSmallScreen? 'flex-start' : 'space-between',
                      marginBottom: 2,
                    }}>
                    {
                      search 
                        && <TextField 
                            id="search"
                            name="search"
                            label="Buscar"
                            value={searchValue}
                            onChange={handleSearchChange}
                            sx={{ 
                              mt: isSmallScreen ? 10 : '', 
                              marginRight: isSmallScreen ? 0 : 1,
                              marginBottom: isSmallScreen ? 1 : 0,
                              textAlign: 'center',
                            }}
                          />
                    }
                    { children }
                  </Box>
                  <Box sx={{ overflowX: 'auto' }}>
                    <Table
                      sx={{ 
                        maxWidth: '100%'
                      }}
                    >
                      <TableHead>
                        <TableRow>
                          {columns.map((column) => (
                            <TableCell
                              key={column.id}
                              onClick={() => column.sortable && handleSortColumn(column.id)}
                              sx={{ whiteSpace: 'nowrap', backgroundColor: themePalette.PRIMARY }}
                            >
                              <Typography variant="body1" sx={{ px: 5, fontSize: 18, color: '#FFF', textAlign: 'left'  }}>
                                {column.label}
                              </Typography>
                            </TableCell>
                          ))}
                          <TableCell sx={{ backgroundColor: themePalette.PRIMARY }}>
                            <Typography variant='body1' sx={{ px: 5, fontSize: 18, color: '#FFF', textAlign: 'left' }}>
                              Acciones
                            </Typography>
                          </TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {
                          isLoading
                          ? (
                              <TableRow>
                                <TableCell>
                                  <Stack spacing={1} maxWidth={100}>
                                    <Skeleton variant="rounded" width={2000} height={60} />
                                    <Skeleton variant="rounded" width={2000} height={60} />
                                  </Stack>
                                </TableCell>
                              </TableRow>
                            )
                          : data.map((row, index) => (
                              <TableRow key={index}>
                                {columns.map((column) => (
                                  <TableCell key={column.id} sx={{ px: 7, textAlign: 'left'}}>{row[column.id]}</TableCell>
                                ))}
                                <TableCell sx={{ px: 7, fontWeight: 'bold', textAlign: 'left'}}>
                                  { showEditOption && <EditButton row={row} handleModalEdit={handleModalEdit} handlePageEdit={handlePageEdit}/> }
                                  { showDeleteOption && <DeleteButton row={row} setShowDelete={setShowDelete}/> }
                                </TableCell>
                              </TableRow>
                            ))
                        }
                      </TableBody>
                    </Table>
                  </Box>

                  <TablePagination
                    rowsPerPageOptions={[5, 10, 25, 50]}
                    labelRowsPerPage="filas por paginas"
                    component="div"
                    count={totalRows}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                    sx={{ fontWeight: 'bold', textAlign: 'center', color: themePalette.SECONDARY}}
                  />
                </Box>
      </CardContent>
    </Card>
  )
}

export default DataTable
