import { RouteObject } from 'react-router-dom'
import Admin from '../../pages/Admin/Index'
import Error from '../../pages/Error'
import Services from '../../pages/Admin/Services'
import Resource from '../../pages/Admin/Resource'
import { ServiceRouter } from './Services'
import { UsersRoute } from './Users'
import { LaboratoryRoute } from './Laboratory'
import ServiceStandardizationNew from '../../pages/Admin/ServiceStandardizationNew'
import AddServiceStandardizations from '../../pages/Admin/AddServiceStandardizations'

const adminRouter: RouteObject[] = [
    {
      path: "/Administrador",
      element: <Admin/>,
      errorElement: <Error/>,
      children: [
        {
          path: "servicios",      
          element: <Services/>,
          errorElement: <Error/>
        },
        ...ServiceRouter,
        {
          path: "recursos",
          element: <Resource/>,
          errorElement: <Error/>,
        },
        ...UsersRoute,
        ...LaboratoryRoute,
        {
          path: "estandarizacionNew",
          element: <ServiceStandardizationNew/>,
          errorElement: <Error/>,
        },
        {
          path: "estandarizacionNew/registrar",
          element: <AddServiceStandardizations/>,
          errorElement: <Error/>,
        },
        {
          path: "estandarizacionNew/editar/:id",
          element: <AddServiceStandardizations/>,
          errorElement: <Error/>,
        },
      ]
    }
]

export { adminRouter}