import { useContext, useEffect, useState } from "react"
import { Autocomplete, Box, Button, Card, CardContent, Grid, TextField, Typography } from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import DataTable from "../../../components/DataTable/DataTable"
import { Column, RowMetodologia, RowMuestra, RowParamsServices, TypesRowsDataTableContext, UseDataFetchResult, fetchDataFunc } from '../../../types/DataTable';
import { ParamsColumnsTable } from "../../../constants/DataTableAtributtesConstants"
import { GetSamplesRequestApi, GetSamplesResponseApi } from '../../../types/API/Sample';
import { GetMetodologiesRequestApi, GetMetodologiesResponseApi } from "../../../types/API/Methology"
import { valueParamService } from "../../../types/API/Params"
import UploadFile from "../../../components/Form/UploadFileImage"
import { initialValueParams, initialValueService } from "../../../constants/FormConstants"
import { validationSchemaService } from "../../../constants/ValidationSchemas"
import { DataForm, valueFormService } from "../../../types/Form"
import useFormValidateHook from "../../../hooks/useFormValidateHook";
import { themePalette } from '../../../config/MaterialUi/Theme.Object';
import '../../../styles/stylesTagForm.css'
import useRequestApi, { MetodoHTTP } from "../../../hooks/useRequestApi"
import { ResponseFormat } from "../../../types/API/Index"
import { CreateServiceRequestApi, CreateServiceResponseApi, GetServicesResponse, ParametrosRequestType, ParametrosUpdateRequestType, UpdateServiceRequestApi, UpdateServiceResponseApi } from "../../../types/API/Services"
import { Toastification } from "../../../context/ToastificationContext"
import { useParams } from "react-router-dom"
import useRequestImages from "../../../hooks/useRequestImages";
import ModalAdd from "./ModalAdd";
import AddMethologyForm from "./AddMethologyForm";
import AddSampleForm from "./AddSampleForm";


const AddServiceFom: React.FC<{}> = () => {
    
    const [showMethology, setShowMethology] = useState<boolean>(false)
    const [showSample, setShowSample] = useState<boolean>(false)
    const [columns, setColumns] = useState<Column[]>([])
    const [optionsMethology, setOptionsMethology] = useState<RowMetodologia[]>([])
    const [optionsSample, setOptionsSample] = useState<RowMuestra[]>([])
    const [searchValueMethology, setSearchValueMethology] = useState<string| undefined>(undefined)
    const [searchValueSample, setSearchValueSample] = useState<string| undefined>()
    const [valueParam, setValueParam] = useState<valueParamService>(initialValueParams)
    const [valuesParams, setValuesParams] = useState<valueParamService[]>([])
    const [title, setTitle] = useState<string>('Registrar')
    const [src, setSrc] = useState<string>()

    const { id: idParams } = useParams()

    const requestMethodology = useRequestApi<ResponseFormat<GetMetodologiesResponseApi>,GetMetodologiesRequestApi>({
        path: '/Recurso/GetMetodologies/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestSample = useRequestApi<ResponseFormat<GetSamplesResponseApi>,GetSamplesRequestApi>({
        path: '/Recurso/GetSamples/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestService = useRequestApi<ResponseFormat<GetServicesResponse>,{id: string}>({
        path: `/Servicio/GetService/${idParams}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const useRequestApiCreate = useRequestApi<ResponseFormat<CreateServiceResponseApi>,CreateServiceRequestApi>({
        path: '/Servicio/CreateService',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const useRequestApiUpdate = useRequestApi<ResponseFormat<UpdateServiceResponseApi>,UpdateServiceRequestApi>({
        path: `/Servicio/UpdateService/${idParams}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.PUT
    })

    const imagesRequest = useRequestImages<string,any>({
        path: 'Servicio/UploadServicePicture',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const imagesRequestGET = useRequestImages<File,string>({
        path: `Servicio/DownloadServicePicture/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const {
        updateNotificationOpen,
        updateTypeNotification,
        updateMessage
    } = useContext(Toastification)

    const handleOpenMethology = () => {
        const showAux = !showMethology
        setShowMethology(showAux)
    } 

    const handleOpenSample = () => {
        const showAux = !showSample
        setShowSample(showAux)
    } 

    const fetchData: fetchDataFunc<RowParamsServices> = (
        page = 0,
        rowsPerPage = 5,
        searchValue = '',
        sortColumn = 'nombre'
      ) => {
        return new Promise<UseDataFetchResult<RowParamsServices>>((resolve) => {
            const valueAux: RowParamsServices[] = valuesParams.map<RowParamsServices>( (o,i) => {
                const aux:RowParamsServices ={
                    id: o.id,
                    nombre: o.name,
                    descripcion: o.description || ''
                }
                return aux
             })
            resolve({
              data: valueAux,
              totalRows: 10
            })
        })
    }   

    const handlePageEdit = (row: TypesRowsDataTableContext | undefined) => {
        const currentRowParams: RowParamsServices = row as RowParamsServices
        setValueParam({
            id: currentRowParams.id,
            name: currentRowParams.nombre,
            description: currentRowParams.descripcion
        })
    }

    const addParams = () => {
        
        setValuesParams( prevValue => {
                const param = prevValue.find( param => param.id === valueParam.id)
                let newParams: valueParamService[]  = [
                    ...prevValue,
                    valueParam
                ]
                
                if ( param && title === 'Editar') {
                    newParams = prevValue.map( p => {
                        if ( p.id === param.id ) {
                            console.log('entre')
                            return ({
                                id: valueParam.id,
                                name: valueParam.name,
                                description: valueParam.description
                            })
                        }
                        return p 
                    })
                }
                return newParams
            }
        )
        setValueParam(initialValueParams)
    }

    useEffect(() => {
        const updateMethologyOptions = async () => {
            const response = await requestMethodology.submit(
                {
                    search: '',
                    pageNumber: 1,
                    pageSize: 5,
                    orderById: false,
                    orderByName: false,
                    orderByStatus: false,
                    orderByDescription: false,
                    onlyActive: false,
                }
            )
            if( !response )
                return
            const { data } = response
            if(!data)
                return
            const { items } = data
            let rows: RowMetodologia[] = []
            rows  = items.map<RowMetodologia>(sample => {
                let row: RowMetodologia = {
                    nombre: sample.nombre,
                    descripcion: sample.descripcion,
                    id: sample.idservmetodologia,
                    estado: sample.deletedAt ? 'INACTIVO' : 'ACTIVO'
                }
                return row
            })
            setOptionsMethology(rows)
        }

        const updateSampleOptions = async () =>  {
            const response = await requestSample.submit(
                {
                    search: '',
                    pageNumber: 1,
                    pageSize: 5,
                    orderById: false,
                    orderByName: false,
                    orderByStatus: false,
                    orderByDescription: false,
                    onlyActive: false,
                }
            )

            if(!response)
                return 
            const { data } = response
            if(!data)
                return
            const { items } = data
            let rows: RowMuestra[] = []
            rows  = items.map<RowMuestra>(sample => {
                let row: RowMuestra = {
                    nombre: sample.nombre,
                    descripcion: sample.descripcion,
                    id: sample.idservmuestra,
                    estado: sample.deletedAt ? 'INACTIVO' : 'ACTIVO'
                }
                return row
            })
            setOptionsSample(rows)
        }
        if(!showMethology){
            updateMethologyOptions()
        }
        if(!showSample)
            updateSampleOptions()
    }, [showSample,showMethology])

    useEffect(() => {
        formik.setFieldValue('params', valuesParams)
        formik.setFieldTouched('params', true, false)
    }, [valuesParams])

    useEffect(() => {
        const requestResource = async() => {
            const  [
                responseMethodology,
                responseSample
            ] = await Promise.all([
                requestMethodology.submit(
                    {
                        search: searchValueMethology ?? '',
                        pageNumber: 1,
                        pageSize: 5,
                        orderById: false,
                        orderByName: false,
                        orderByStatus: false,
                        orderByDescription: false,
                        onlyActive: false,
                    }
                ),
                requestSample.submit(
                    {
                        search: searchValueSample ?? '',
                        pageNumber: 1,
                        pageSize: 5,
                        orderById: false,
                        orderByName: false,
                        orderByStatus: false,
                        orderByDescription: false,
                        onlyActive: false,
                    }
                ),             
            ])
            if(responseMethodology){
                const { data } = responseMethodology
                if(!data)
                    return
                const { items } = data
                let rows: RowMetodologia[] = []
                rows  = items.map<RowMetodologia>(sample => {
                    let row: RowMetodologia = {
                        nombre: sample.nombre,
                        descripcion: sample.descripcion,
                        id: sample.idservmetodologia,
                        estado: sample.deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                setOptionsMethology(rows)
            }
            if(responseSample){
                const { data } = responseSample
                if(!data)
                    return
                const { items } = data
                let rows: RowMuestra[] = []
                rows  = items.map<RowMuestra>(sample => {
                    let row: RowMuestra = {
                        nombre: sample.nombre,
                        descripcion: sample.descripcion,
                        id: sample.idservmuestra,
                        estado: sample.deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                setOptionsSample(rows)
            }
        }
        requestResource()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [searchValueMethology,searchValueSample])


    useEffect(() => {
        const asyncRequestGetService = async () => {
            const response = await requestService.submit({id: idParams??''})
            console.log('response: ', response)
            if ( !response )
                return
    
            const { data } = response
            
            if ( !data )
                return
            
            const { metodologias, muestras, parametros, servicio } = data
            
            setSrc(servicio.imagen)
    
            const currentMethology = metodologias.map<RowMetodologia>( methology => ({
                id: methology.idservmetodologia,
                nombre: methology.nombre,
                descripcion: methology.descripcion,
                estado: methology.deletedAt ? 'INACTIVO' : 'ACTIVO'
            }))
    
            const currentlySample = muestras.map<RowMuestra>( sample => ({
                id: sample.idservmuestra,
                nombre: sample.nombre,
                descripcion: sample.descripcion,
                estado: sample.deletedAt ? 'INACTIVO' : 'ACTIVO'
            }))
    
            const currentlyParams = parametros.map<valueParamService>( param => ({
                id: param.idservparametroservicio,
                name: param.nombre,
                description: param.descripcion
            }))
    
    
            setValuesParams(currentlyParams)
            handleOtherFieldChange(servicio.nombre,"name")
            handleOtherFieldChange(servicio.descripcion,"description")
            handleOtherFieldChange(currentMethology,"methology")
            handleOtherFieldChange(currentlySample,"sample")
        }

        if (idParams)
            asyncRequestGetService()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [title])

    useEffect(() => {
        setColumns(ParamsColumnsTable)
        setSearchValueSample('')
        
        
        if (idParams) {
            setTitle('Editar')
            
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        const requestAsyncImage = async () => {
            const fileImage = await imagesRequestGET.submit(src??'')
            if ( !fileImage )
                return
            //formik.values.image = fileImage
            const blob = new Blob([fileImage], { type: 'image/jpeg' })
            handleOtherFieldChange(blob,"image")
        }
        if (idParams)
            requestAsyncImage()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [src])
    
    const handleSubmit = async (data: DataForm) => {
        const dataAux = data as valueFormService
        const { name, description, params, methology, sample, image } = dataAux
        const formData = new FormData()
        if (!image)
            return
        formData.append('file', image)
        let urlImage = await imagesRequest.submit(formData)  

        if(typeof urlImage === 'number')
            urlImage = src ?? ''

        const metodologias = methology.map<number>( m => m.id)
        const muestras = sample.map<number>( s => s.id)
        const parametros = params.map<ParametrosRequestType>( p => ({
            nombre: p.name,
            descripcion: p.description??''
        }))

        if ( idParams ) {
            const payload: UpdateServiceRequestApi = {
                servicio: {
                    idservservicio: Number(idParams),
                    nombre: name,
                    descripcion: description,
                    imagen: urlImage ?? ''
                },
                metodologias,
                muestras,
                parametros: params.map<ParametrosUpdateRequestType>( p => ({
                    idservparametroservicio: Number(p.id ?? 0),
                    nombre: p.name,
                    descripcion: p.description ?? '',
                    servicioId: Number(idParams)
                }))
            }
            const response = await useRequestApiUpdate.submit(payload)
            if( !response )
            return
            const { message, succeeded } = response
            if (succeeded)  {
                updateMessage(message)
                updateNotificationOpen(true)
                setTimeout(() => {
                    updateTypeNotification(true)
                    updateNotificationOpen(false)
                    window.location.href = '/Administrador/servicios'
                } ,1000)
            } else {
                updateMessage(message)
                updateTypeNotification(false)
                updateNotificationOpen(true)
            }
        } else {
            const payload: CreateServiceRequestApi = {
                servicio: {
                    nombre: name,
                    descripcion: description,
                    imagen: urlImage ?? ''
                },
                metodologias,
                muestras,
                parametros
            }
            const response = await useRequestApiCreate.submit(payload)
            if( !response )
                return
            const { message, succeeded } = response
            if (succeeded)  {
                updateMessage(message)
                updateNotificationOpen(true)
                setTimeout(() => {
                    updateTypeNotification(true)
                    updateNotificationOpen(false)
                    window.location.href = '/Administrador/servicios'
                } ,1000)
            } else {
                updateMessage(message)
                updateTypeNotification(false)
                updateNotificationOpen(true)
            }
            
        }
    }

    const {
        HandleTextFieldChange,
        handleTextFieldBlur,
        handleOtherFieldChange,
        handleFileChange,
        formik
    } = useFormValidateHook({
        initialValues: initialValueService,
        validationSchema: validationSchemaService,
        onSubmit: handleSubmit
    })  
    
    return (
        <form onSubmit={formik.handleSubmit}>
            <Grid container spacing={2} >
                <Grid
                    item
                    xs={12}
                >
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2 }} >
                        <CardContent>
                        <Typography
                            color={themePalette.PRIMARY}
                            variant="h4"
                        >
                            { title } Servicio
                        </Typography>
                        <Typography
                            sx={{ mt: 2 }}
                            color={themePalette.PRIMARY}
                            variant="h5"
                        >
                            Informacin General del servicio
                        </Typography>
                        <Grid container item spacing={2}>
                            <Grid item md={6} xs={12}>
                                <Box sx={{ width: "100%", mt: 2}}>
                                    <TextField
                                        id="name"
                                        label="Nombre"
                                        value={formik.values.name}
                                        onChange={HandleTextFieldChange}
                                        onBlur={handleTextFieldBlur}
                                        error={formik.touched.name && Boolean(formik.errors.name)}
                                        helperText={formik.touched.name && formik.errors.name}
                                        variant="outlined"
                                        fullWidth
                                    />
                                </Box>
                                <Box sx={{ width: "100%", mt: 2}}>
                                    <TextField
                                        id="description"
                                        multiline
                                        rows={7}
                                        label="Descripcin"
                                        color="primary"
                                        value={formik.values.description}
                                        onChange={HandleTextFieldChange}
                                        onBlur={handleTextFieldBlur}
                                        error={formik.touched.description && Boolean(formik.errors.description)}
                                        helperText={formik.touched.description && formik.errors.description}
                                        variant="outlined"
                                        fullWidth
                                    />
                                </Box>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Box sx={{ width: "100%", mt: 2}}>
                                    <UploadFile
                                        image={true}
                                        handleValue={handleFileChange}
                                        value={formik.values.image as File | undefined}
                                        src={src}
                                        type="image/"
                                        name="image"
                                    />
                                </Box>
                            </Grid>
                        </Grid>
                        
                        </CardContent>
                    </Card>
                </Grid>
                <Grid
                    item
                    xs={12}
                    md={12}
                >
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2 }} >
                        <CardContent>
                            <Grid container item spacing={2}>
                                <Grid container item xs={12} spacing={2}>
                                    <Grid item  md={8} xs={12}>
                                        <Typography
                                            sx={{ mb: 2}}
                                            color={themePalette.PRIMARY}
                                            variant="h5"
                                        >
                                            Asignar recursos al servicio {'(metodologas y muestras)'}
                                        </Typography> 
                                    </Grid>
                                    <Grid container item spacing={2} md={4} xs={12} sx={{ px: 2 }}>
                                        <Grid item md={6} xs={12}>
                                            <Button type="button" sx={{ width: '100%' }} variant="outlined" startIcon={<AddIcon />} onClick={handleOpenMethology}>
                                                Metodologa
                                            </Button>
                                        </Grid>
                                        <Grid item md={6} xs={12}>
                                            <Button type="button" sx={{ width: '100%' }} variant="outlined" startIcon={<AddIcon />} onClick={handleOpenSample}>
                                                Muestra
                                            </Button>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid container item xs={12} spacing={2} >       
                                    <Grid md={6} xs={12} sx={{px:2, mt:2}}>
                                        <Box sx={{ width: '100%'}} >
                                            <Autocomplete
                                                disablePortal
                                                multiple
                                                id="methology"
                                                options={optionsMethology}
                                                value={formik.values.methology as RowMetodologia[]}
                                                getOptionLabel={(option) => option.nombre}
                                                sx={{
                                                    borderRadius: 10
                                                }}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Metodologa" 
                                                    />
                                                )}
                                                onInputChange={(event, newValue) => {
                                                    setSearchValueMethology(newValue)
                                                }}
                                                onChange={(event, newInputValue: RowMetodologia[]) => {
                                                    handleOtherFieldChange(newInputValue,"methology")
                                                }}
                                                isOptionEqualToValue={(option, value) => option.id === value.id}
                                                fullWidth
                                            />
                                        </Box>
                                    </Grid>            
                                    <Grid md={6} xs={12} sx={{px:2, mt:2}}>   
                                        <Box sx={{ width: '100%'}}>
                                            <Autocomplete
                                                disablePortal
                                                multiple
                                                id="sample"
                                                options={optionsSample}
                                                value={formik.values.sample as RowMuestra[]}
                                                getOptionLabel={(option) => option.nombre}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Muestra"
                                                        sx={{
                                                            '& label': {
                                                            color: themePalette.PRIMARY, // Cambia el color del label a azul
                                                            
                                                            },
                                                        }}
                                                    />
                                                )}
                                                onInputChange={(event, newValue) => {
                                                    setSearchValueSample(newValue)
                                                }}
                                                onChange={(event, newInputValue: RowMuestra[]) => {
                                                    handleOtherFieldChange(newInputValue,"sample")
                                                }}
                                                isOptionEqualToValue={(option, value) => option.id === value.id}
                                                fullWidth
                                            />
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid 
                    container
                    item
                    xs={12}
                    md={12}
                    spacing={2}
                >             
                    <Grid item xs={12}>
                        <DataTable<RowParamsServices>
                            columns={columns}
                            fetchData={fetchData}
                            handlePageEdit={handlePageEdit}
                            search={false}
                        >
                            <Grid item xs={12}>
                                <Box sx={{ width: "100%", mt: 1 }}>
                                    <Typography
                                        color={themePalette.PRIMARY}
                                        variant="h4"
                                    >
                                        Parametros
                                    </Typography>
                                    <Grid item xs={12}>
                                        <Box sx={{ width: "100%", mt: 2 }}>
                                            <TextField
                                                id="nameParam"
                                                label="Nombre"
                                                color="primary"
                                                value={valueParam.name}
                                                onChange={(e) => {
                                                    setValueParam({
                                                ...valueParam,
                                                name: e.target.value
                                                    })
                                                }}
                                                variant="outlined"
                                                fullWidth
                                            />
                                        </Box>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Box sx={{ width: "100%", mt: 1 }}>
                                            <TextField
                                                multiline
                                                rows={3}
                                                id="descriptionParam"
                                                label="Descripcion"
                                                color="primary"
                                                value={valueParam.description}
                                                onChange={(e) => {
                                                    setValueParam({
                                                        ...valueParam,
                                                        description: e.target.value
                                                    })
                                                }}
                                                variant="outlined"
                                                fullWidth
                                            />
                                        </Box>
                                    </Grid>
                                    <Button 
                                        type="button"
                                        variant="contained"
                                        color="primary"
                                        sx={{ 
                                            mt: 2,
                                            height: "60px",
                                            width: '25%'
                                        }}
                                        onClick={addParams}
                                    >   
                                        Agregar Parametro
                                    </Button>
                                </Box>
                            </Grid>
                        </DataTable>
                        <Box
                            sx={{
                                width: '100%'
                            }}
                        >
                            <Button 
                                type="submit"
                                variant="contained"
                                color="primary"
                                sx={{ 
                                    mt: 2,
                                    height: "60px",
                                    width: '50%'
                                }}
                            >
                                Guardar
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
                <ModalAdd
                    show={showMethology}
                    setShow={setShowMethology}
                >
                    <AddMethologyForm/>
                </ModalAdd>
                <ModalAdd
                    show={showSample}
                    setShow={setShowSample}
                >
                    <AddSampleForm/>
                </ModalAdd>
            </Grid>
        </form>
    )
}

export default AddServiceFom