import React, { useContext, useEffect, useState } from 'react'
import { Avatar, Card, CardContent, Chip, Container, Grid, List, ListItem, ListItemAvatar, ListItemText, Typography } from '@mui/material'
import EditRequestForm from './components/EditRequestForm'
import { themePalette } from '../../config/MaterialUi/Theme.Object'
import { useParams } from 'react-router-dom'
import useRequestApi, { MetodoHTTP } from '../../hooks/useRequestApi'
import { ResponseFormat } from '../../types/API/Index'
import { ChangeRequestStatusRequestApi, ChangeRequestStatusResponseApi, Estado, GetAllStatusResponseApi, GetRequestApi, Request } from '../../types/API/Request'
import { AuthContext } from '../../context/AuthContext'
import { UserData } from '../../types/DataUser'
import { UsersInfoRequestApi, UsersInfoResponseApi } from '../../types/API/User'
import PersonIcon from '@mui/icons-material/Person';
import BadgeIcon from '@mui/icons-material/Badge';
import PhoneIcon from '@mui/icons-material/Phone';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';

const EditRequest: React.FC = () => {

    const [title, setTitle] = useState<string>('')
    const [currentRequest, setCurrentRequest] = useState<Request>()
    const [statusOptions, setStatusOptions] = useState<Estado[]>()
    const [currentClientInfo, setCurrentClientInfo] = useState<any>() 
    const { id } = useParams()

    const requestGetInfoUser = useRequestApi<ResponseFormat<UsersInfoResponseApi>,UsersInfoRequestApi>({
        path: '/Auth/UsersInfo/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetRequest = useRequestApi<ResponseFormat<GetRequestApi>,string>({
        path: `/Solicitud/GetRequest/${id}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const requestChangeRequestStatus = useRequestApi<ResponseFormat<ChangeRequestStatusResponseApi>,ChangeRequestStatusRequestApi>({
        path: `/Solicitud/ChangeRequestStatus`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetAllStatus = useRequestApi<ResponseFormat<GetAllStatusResponseApi>,string>({
        path: `/Solicitud/GetAllStatus/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const { 
        userData
    } =  useContext(AuthContext)


    const getColor = () => {
        switch (currentRequest?.estadoId??0) {
            case 1:
                return 'default'
            case 2: 
                return 'primary'
            case 3:
                return 'warning'
            case 4:
                return 'default'
            case 5:
                return 'success'
            default:
                return 'default'
        }
        
    }

    useEffect(() => {
        const getAsyncAllState = async () => {
            console.log('entre')
            const response = await requestGetAllStatus.submit('')
            
            if(!response)
                return
            
            const { data } = response

            if (!data)
                return
            
            setStatusOptions(data)
        }
        getAsyncAllState()
    }, [currentRequest])

    useEffect(() => {
        const asyncChangeStatusRequest = async () => {
            if ( currentRequest?.estadoId === 1){
                await requestChangeRequestStatus.submit({
                    estadoId: 3,
                    solicitudId: currentRequest.idsolsolicitud,
                    usuarioSilabId: (userData as UserData).useraccountid
                })
                
            }
        }
        
        asyncChangeStatusRequest()
    }, [currentRequest])

    useEffect(() => {
        const asyncRequestClientInfo = async () => {
            console.log('currentRequest: ', currentRequest?.estadoId)
            if(currentRequest){ 
                const payloadInfoClient:UsersInfoRequestApi = [
                    currentRequest.clienteId
                ]

                const response = await requestGetInfoUser.submit(payloadInfoClient)
            
                
                if (!response)
                    return

                console.log('userInfo:' , response)
                setCurrentClientInfo(response)
                const { data } = response
                
                if (!data)
                    return
                setCurrentClientInfo(data)   
            }
        }

        if(currentRequest)
            asyncRequestClientInfo()
    }, [currentRequest])

    useEffect(() => {
        const asyncGetRequest = async () => {
            const response = await requestGetRequest.submit('')

            if (!response)
            return

            const { data } = response
            
            if( !data )
            return
            setCurrentRequest(data)
        }
        if (id)
            asyncGetRequest()
    }, [title])

    useEffect(() => {
        if ( id )
            setTitle('Edtar')
    }, [id])

    return (
        <Container maxWidth="lg" >
            <Grid container spacing={2}>
                <Grid
                    item
                    xs={12}
                    md={5}
                >   
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2 }} >
                        <CardContent>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="h4"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                Informacin del cliente
                            </Typography>
                            <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <PersonIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].nombre} ${currentClientInfo&&currentClientInfo[0].apellido}`} secondary="Nombre" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <BadgeIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={` ${currentClientInfo&&currentClientInfo[0].identificacion}`} secondary="Cdula" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <PhoneIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].telefono}`} secondary="Telefono" />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar sx={{backgroundColor: themePalette.PRIMARY}}>
                                            <AlternateEmailIcon />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary={`${currentClientInfo&&currentClientInfo[0].correo}`} secondary="Correo" />
                                </ListItem>
                            </List>
                        </CardContent>
                    </Card>
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2, py: 2, mt: 2}} >
                        <CardContent>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="h4"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                Estado de la solicitud
                            </Typography>
                            <Typography
                                color={themePalette.PRIMARY}
                                variant="body1"
                                sx={{
                                    my: 2,
                                    mx: 1
                                }}
                            >
                                <Chip
                                    color={getColor()}
                                    label={(statusOptions?.find( statu => statu.idsolestado === currentRequest?.estadoId))?.nombre ?? '' }
                                />
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid
                    item
                    xs={12}
                    md={7}
                >
                    <Card sx={{ borderWidth:'bold', borderRadius: 2, px: 2 }} >
                        <CardContent>
                            <EditRequestForm
                                idRequest={id}
                                currentRequest={currentRequest}
                            />   
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    )
}

export default EditRequest