import { RouteObject } from 'react-router-dom'
import Error from '../../pages/Error'
import Users from '../../pages/Admin/Users'
import AddUser from '../../pages/Admin/AddUser'

const UsersRoute: RouteObject[] = [
    {
      path: "usuarios",
      element: <Users/>,
      errorElement: <Error/>,
    },
    {
      path: "usuarios/registro",
      element: <AddUser/>,
      errorElement: <Error/>,
    }
]

export { UsersRoute }