import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { Box, Button, Container } from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import DataTable from '../../components/DataTable/DataTable'
import { Column, RowServiceStandardization, TypesRowsDataTableContext, fetchDataFunc } from '../../types/DataTable'
import useRequestApi, { MetodoHTTP } from '../../hooks/useRequestApi'
import { ResponseFormat } from '../../types/API/Index'
import { GetStandardServicesRequestApi, GetStandardServicesResponseApi } from '../../types/API/ServiceStandardization'
import { ServiceStandardizationColumnsTable } from '../../constants/DataTableAtributtesConstants'
import DeleteServiceStandardization from './components/DeleteService copy'

const ServiceStandardizationNew: React.FC = () => {

    const [columns, setColumns] = useState<Column[]>([])
    const [showDelete, setShowDelete] = useState<boolean>(false)

    const navigate = useNavigate()

    const requestServicesStandardization = useRequestApi<ResponseFormat<GetStandardServicesResponseApi>,GetStandardServicesRequestApi>({
        path: '/ServicioEstandarizado/GetStandardServices/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const fetchData: fetchDataFunc<RowServiceStandardization> = async (
        page = 1,
        rowsPerPage = 10,
        searchValue = '',
    ) => {
        const payload: GetStandardServicesRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: false,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            onlyActive: true,
        }

        let totalRows = 0
        let rows: RowServiceStandardization[] = []

        const response = await requestServicesStandardization.submit(payload)

        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowServiceStandardization>(serviceStandardization => {
                    const { nombre: servicio } = serviceStandardization.departamento.servicio?? ''
                    const { nombre: metodologia } =  serviceStandardization.departamento.metodologia?? ''
                    const { nombre: muestra } = serviceStandardization.departamento.muestra?? ''
                    const { nombre: parametro } = serviceStandardization.departamento.parametroServicio?? ''
                    const { nombre: laboratorio } = serviceStandardization.laboratorio?? ''
                    const { acreditado, muestreo, idservinfoservdepartamento: id, tiempoEstimado: tiempo, costo } = serviceStandardization.departamento
                    let row: RowServiceStandardization = {
                        servicio,
                        id,
                        laboratorio,
                        acreditado: acreditado ? 'SI' : 'NO' ,
                        muestreo: muestreo ? 'SI' : 'NO' ,
                        metodologia,
                        muestra,
                        parametro,
                        tiempo,
                        costo
                    }
                    return row
                })
                totalRows = totalCount
            }
        }

        return {
            data: rows,
            totalRows
        }
    }

    const handlePageEdit = (currentRowSelected: TypesRowsDataTableContext | undefined) => {
        const currentServiceStandardization: RowServiceStandardization = currentRowSelected as RowServiceStandardization
        navigate(`editar/${currentServiceStandardization.id}`)
    }

    const handleModalDelete = () => {
        setShowDelete(true)
    }

    useEffect(() => {
        setColumns(ServiceStandardizationColumnsTable)
    }, [])

    return (
        <Container maxWidth="lg">
            <Box
                sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between'
                }}
            >
                <DataTable<RowServiceStandardization>
                    columns={columns}
                    fetchData={fetchData}
                    handlePageEdit={handlePageEdit}
                    setShowDelete={handleModalDelete}
                >
                    <Link to='registrar' style={{ textDecoration: 'none' }}>
                        <Button 
                            sx={{ minHeight: '50px', mt: 1}}
                            variant="contained"
                            startIcon={<AddIcon />}
                        >
                            Crear Servicio Estandarizado
                        </Button>
                    </Link>
                </DataTable>
            </Box>
            <DeleteServiceStandardization
                showDelete={showDelete}
                setShowDelete={setShowDelete}
            />
        </Container>
    )
}

export default ServiceStandardizationNew