import { createContext, useState } from 'react';

export interface ContextTostificationProps {
    notificationOpen: boolean,
    message: string,
    updateMessage: (value: string) => void,
    updateNotificationOpen: (value: boolean) => void,
    typeNotification: boolean,
    updateTypeNotification: (value: boolean) => void,
}


export const Toastification = createContext<ContextTostificationProps>({} as ContextTostificationProps )

export const ToastificationProvider = (
    {
        children
    }: any
) => {

    const [notificationOpen, setNotificationOpen] = useState<boolean>(false)
    const [message, setMessage] = useState<string>('')
    const [typeNotification, setTypeNotification] = useState<boolean>(true)

    const updateMessage = (value: string) => {
        setMessage(value)
    }

    const updateNotificationOpen = (value: boolean) => {
        setNotificationOpen(value)
    }

    const updateTypeNotification = (value: boolean) => {
        setTypeNotification(value)
    }

    return (
        <Toastification.Provider
            value={{
                notificationOpen,
                message,
                updateMessage,
                updateNotificationOpen,
                typeNotification,
                updateTypeNotification
            }}
        >
            { children }
        </Toastification.Provider>
    )
}