import { useContext, useEffect, useState } from 'react'
import { Button, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent } from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import { Column, RowMetodologia, fetchDataFunc } from '../../../types/DataTable'
import { MetodologiaColumnsTable } from '../../../constants/DataTableAtributtesConstants'
import DataTable from '../../../components/DataTable/DataTable'
import ModalAdd from './ModalAdd'
import AddMethologyForm from './AddMethologyForm'
import DeleteMethology from './DeleteMethology'
import { ResponseFormat } from '../../../types/API/Index'
import { GetMetodologiesRequestApi, GetMetodologiesResponseApi } from '../../../types/API/Methology'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { DataTableContext } from '../../../context/DataTableContext'

const TabMethodology: React.FC = () => {
    const [columns, setColumns] = useState<Column[]>([])
    const [show, setShow] = useState<boolean>(false)
    const [showDelete, setShowDelete] = useState<boolean>(false)
    const [onlyActiveService, setOnlyActiveService] = useState(false)

    const { submit } = useRequestApi<ResponseFormat<GetMetodologiesResponseApi>,GetMetodologiesRequestApi>({
        path: '/Recurso/GetMetodologies/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })


    const fetchDataMetodologia: fetchDataFunc<RowMetodologia> = async (
        page = 1,
        rowsPerPage = 5,
        searchValue = '',
        sortColum = '',
        onlyActivate = onlyActiveService
    ) => {
    
        const payload: GetMetodologiesRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: false,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            onlyActive: onlyActivate,
        }

        let totalRows = 0
        let rows: RowMetodologia[] = []
        
        const response = await submit(payload)
        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowMetodologia>(methology => {
                    let row: RowMetodologia = {
                        nombre: methology.nombre,
                        descripcion: methology.descripcion,
                        id: methology.idservmetodologia,
                        estado: methology.deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                totalRows = totalCount
            }
        }
        return {
          data: rows,
          totalRows: totalRows
        }
    }


    const handleOpen = () => {
        const showAux = !show
        setShow(showAux)
    } 
    
    const handleModalEdit = () => {
        const showAux = !show
        setShow(showAux)
    }

    const handleModalDelete = () => {
        const showDelete = !show
        setShowDelete(showDelete)
    }

    useEffect(() => {
        setColumns(MetodologiaColumnsTable)
    }, [])

    return (
        <>
            <DataTable<RowMetodologia>
              columns={columns}
              fetchData={fetchDataMetodologia}
              handleModalEdit={handleModalEdit}
              setShowDelete={handleModalDelete}
              estado={onlyActiveService}
            >
                <FormControl sx={{ width: 200 }}>
                    <InputLabel id="estado">Estado</InputLabel>
                    <Select
                        labelId="estado"
                        id="estado"
                        value={onlyActiveService ? '1' : '0'}
                        label="Estado"
                        onChange={(event: SelectChangeEvent) => {
                            console.log('valor: ', event.target.value )
                            const value = (Number(event.target.value) === 1) ? true : false
                            console.log('estado: ', value)
                            setOnlyActiveService(value)
                        }}
                    >
                        <MenuItem value={1}>ACTIVOS</MenuItem>
                        <MenuItem value={0}>TODOS</MenuItem>
                    </Select>
                </FormControl>
                <Button
                    sx={{ minHeight: '50px', mt: 1}}
                    variant="contained"
                    startIcon={<AddIcon />}
                    onClick={handleOpen}
                >
                    Agregar Metodologia
                </Button>
            </DataTable>
            <ModalAdd
                show={show}
                setShow={setShow}
            >
                <AddMethologyForm/>
            </ModalAdd>
            <DeleteMethology
                showDelete={showDelete}
                setShowDelete={setShowDelete}
            />
        </>
    )
}

export default TabMethodology