import { RouteObject } from 'react-router-dom'
import Laboratories from '../../pages/Admin/Laboratories'
import Error from '../../pages/Error'
import AddLaboratory from '../../pages/Admin/AddLaboratory'

const LaboratoryRoute: RouteObject[] = [
    {
        path: "laboratorios",
        element: <Laboratories/>,
        errorElement: <Error/>,
    },
    {
        path: "laboratorios/registrar",
        element: <AddLaboratory/>,
        errorElement: <Error/>,
    },
    {
        path: "laboratorios/registrar/:id",
        element: <AddLaboratory/>,
        errorElement: <Error/>,
    }
]

export { LaboratoryRoute }