import { useContext, useEffect, useState } from 'react';
import { Box, Button, TextField } from '@mui/material'
import { DataForm, valueFormMethology } from '../../../types/Form'
import { initialValueMethology } from '../../../constants/FormConstants'
import { validationSchemaMethology } from '../../../constants/ValidationSchemas'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { DataTableContext } from '../../../context/DataTableContext'
import { RowMetodologia } from '../../../types/DataTable'
import { CreateMetodology, CreateMetodologyResponseApi, UpdateMetodologyRequestApi, UpdateMetodologyResponseApi,  } from '../../../types/API/Methology'
import { ResponseFormat } from '../../../types/API/Index'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { Toastification } from '../../../context/ToastificationContext';

const AddMethologyForm: React.FC = () => {

  const [title, setTitle] = useState<string>('Registrar')

  const { 
    currentRowSelected,
    setRow,
    setDataContext,
    dataAux
  } = useContext(DataTableContext)

  const {
    updateNotificationOpen,
    updateMessage,
    updateTypeNotification
  } = useContext(Toastification)
  
  const useRequestApiCreate = useRequestApi<ResponseFormat<CreateMetodologyResponseApi>,CreateMetodology>({
    path: '/Recurso/CreateMetodology',
    url: process.env.REACT_APP_API_GATEWAY,
    method: MetodoHTTP.POST
  })

  const useRequestApiUpdate = useRequestApi<ResponseFormat<UpdateMetodologyResponseApi>,UpdateMetodologyRequestApi>({
    path: `/Recurso/UpdateMetodology/${(currentRowSelected as RowMetodologia)?.id}`,
    url: process.env.REACT_APP_API_GATEWAY,
    method: MetodoHTTP.PUT
  })

  const handleSubmit = async (dataForm: DataForm) => {
    const dataFormAux = dataForm as valueFormMethology

    if ( currentRowSelected ){
      onEdit(dataFormAux)
      return
    }

    const payload: CreateMetodology = {
      nombre: dataFormAux.name,
      descripcion: dataFormAux.description
    }


    const response = await useRequestApiCreate.submit(payload)
    if( !response )
      return
    const { succeeded, message } = response

    
    if ( succeeded ) {
      updateMessage(message)
      updateNotificationOpen(true)
      setDataContext([
        {
          nombre: dataFormAux.name,
          descripcion: dataFormAux.description
        },
        ...dataAux,
      ])
    } else {
      updateMessage(message)
      updateTypeNotification(false)
      updateNotificationOpen(true)
    }

    setTimeout(() => {
      updateTypeNotification(true)
      updateNotificationOpen(false)
    } ,3000)
  }

  const onEdit = async (dataForm: valueFormMethology) => {
    const currentRow = currentRowSelected as RowMetodologia
    const response = await useRequestApiUpdate.submit({
      nombre: dataForm.name,
      descripcion: dataForm.description,
      idservmetodologia: currentRow.id
    })
    if (!response)
      return
    const { succeeded, message } = response
    
    if(succeeded){
      const data = (dataAux as RowMetodologia[]).map( metodoly => {
        let auxMethology: RowMetodologia = metodoly
        if ( metodoly.id === currentRow.id ) {
          auxMethology.nombre = dataForm.name
          auxMethology.descripcion = dataForm.description
        }
        return auxMethology
      })
      updateMessage(message)
      updateNotificationOpen(true)
      setDataContext(data)
      setRow(undefined)
    }
    else {
      updateMessage(message)
      updateTypeNotification(false)
      updateNotificationOpen(true)
    }

    setTimeout(() => {
      updateTypeNotification(true)
      updateNotificationOpen(false)
    } ,3000)
  }

  const {
    HandleTextFieldChange,
    handleTextFieldBlur,
    handleOtherFieldChange,
    formik
  } = useFormValidateHook({
    initialValues: initialValueMethology,
    validationSchema: validationSchemaMethology,
    onSubmit: handleSubmit
  })  


  useEffect(() => {
    if ( currentRowSelected ){
      console.log('currentRowSelected: ', currentRowSelected)
      const { nombre, descripcion } = currentRowSelected as RowMetodologia
      handleOtherFieldChange(nombre,"name")
      handleOtherFieldChange(descripcion,"description")
      setTitle('Editar')
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentRowSelected])

  return (
    <div>
        <h1>
          { title } Metodologia
        </h1>
        <form onSubmit={formik.handleSubmit}>
          <Box>
            <TextField
                id="name"
                name='name'
                label="Nombre"
                value={formik.values.name}
                onChange={HandleTextFieldChange}
                onBlur={handleTextFieldBlur}
                error={formik.touched.name && Boolean(formik.errors.name)}
                helperText={formik.touched.name && formik.errors.name}
                variant="outlined"
                fullWidth
                color="primary"
            />
          </Box>
          <Box sx={{ mt: 2 }}>
            <TextField
              id="description"
              name="description"
              multiline
              rows={3}
              label="Descripcin"
              value={formik.values.description}
              onChange={HandleTextFieldChange}
              onBlur={handleTextFieldBlur}
              error={formik.touched.description && Boolean(formik.errors.description)}
              helperText={formik.touched.description && formik.errors.description}
              variant="outlined"
              fullWidth
              color="primary"
            />
          </Box>
          <Box  sx={{ mt: 2, width: '100%' }}>
            <Button
              type='submit'
              variant='contained'
            >
              Guardar
            </Button>
          </Box>
        </form>
    </div>
  )
}

export default AddMethologyForm
