import { useEffect, useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { Box, Button, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent } from "@mui/material"
import AddIcon from '@mui/icons-material/Add'
import DataTable from "../../components/DataTable/DataTable"
import { LaboratoriesColumnsTable } from "../../constants/DataTableAtributtesConstants"
import { Column, RowLaboratories, TypesRowsDataTableContext, fetchDataFunc } from "../../types/DataTable"
import { GetLaboratoriesRequestApi, GetLaboratoriesResponseApi } from "../../types/API/Laboratories"
import useRequestApi, { MetodoHTTP } from "../../hooks/useRequestApi"
import { ResponseFormat } from "../../types/API/Index"
import DeleteLaboratory from "./components/DeleteLaboratory"


const Laboratories = () => {
    const [ columns, setColumns ] = useState<Column[]>([])
    const [showDelete, setShowDelete] = useState<boolean>(false)
    const [onlyActiveService, setOnlyActiveService] = useState(false)

    const navigate = useNavigate()
    
    const { submit } = useRequestApi<ResponseFormat<GetLaboratoriesResponseApi>,GetLaboratoriesRequestApi>({
        path: '/Laboratorio/GetLaboratories',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const fetchData: fetchDataFunc<RowLaboratories> = async (
        page = 1,
        rowsPerPage = 5,
        searchValue = '',
        sortColum = '',
        onlyActivate = onlyActiveService
    ) => {
        const payload: GetLaboratoriesRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: false,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            onlyActive: onlyActivate,
        }

        let totalRows = 0
        let rows: RowLaboratories[] = []

        const response = await submit(payload)

        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowLaboratories>(laboratory => {
                    const { nombre, idlablaboratorios, descripcion, deletedAt } = laboratory.laboratorio
                    let row: RowLaboratories = {
                        nombre,
                        descripcion,
                        id: idlablaboratorios,
                        estado: deletedAt ? 'INACTIVO' : 'ACTIVO'
                    }
                    return row
                })
                totalRows = totalCount
            }
        }

        return {
            data: rows,
            totalRows
        }
    }

    const handlePageEdit = (currentRowSelected: TypesRowsDataTableContext | undefined) => {
        const currentService: RowLaboratories = currentRowSelected as RowLaboratories
        navigate(`registrar/${currentService.id}`)
    }

    const handleModalDelete = () => {
        setShowDelete(true)
    }

    useEffect(() => {
      setColumns(LaboratoriesColumnsTable)
    }, [])

    return (
        <Container
            maxWidth="xl"
        >
            <Box>
                <DataTable<RowLaboratories>
                    columns={columns}
                    fetchData={fetchData}
                    handlePageEdit={handlePageEdit}
                    setShowDelete={handleModalDelete}
                    estado={onlyActiveService}
                >
                    <FormControl sx={{ width: 200 }}>
                        <InputLabel id="estado">Estado</InputLabel>
                        <Select
                            labelId="estado"
                            id="estado"
                            value={onlyActiveService ? '1' : '0'}
                            label="Estado"
                            onChange={(event: SelectChangeEvent) => {
                                console.log('valor: ', event.target.value )
                                const value = (Number(event.target.value) === 1) ? true : false
                                console.log('estado: ', value)
                                setOnlyActiveService(value)
                            }}
                        >
                            <MenuItem value={1}>ACTIVOS</MenuItem>
                            <MenuItem value={0}>TODOS</MenuItem>
                        </Select>
                    </FormControl>
                    <Link to='registrar' style={{ textDecoration: 'none' }}>
                        <Button 
                            sx={{ minHeight: '50px', mt: 1}}
                            variant="contained"
                            startIcon={<AddIcon />}
                        >
                                Agregar Laboratorio
                        </Button>
                    </Link>
                </DataTable>
            </Box>
            <DeleteLaboratory
                showDelete={showDelete}
                setShowDelete={setShowDelete}
            />
        </Container>
    )
}

export default Laboratories