import { useContext, useEffect, useState } from "react"
import useRequestApi, { MetodoHTTP } from "../hooks/useRequestApi"
import { ResponseFormat } from "../types/API/Index"
import { LoginRequestApi, LoginResponseApi } from "../types/API/Login"
import { AuthContext } from "../context/AuthContext"
import { Toastification } from "../context/ToastificationContext"
import { Backdrop, CircularProgress } from "@mui/material"

const Index = () => {
    
    const [email, setEmail] = useState<string>()
    const [password, setPassword] = useState<string>()
    const requestLogin = useRequestApi<ResponseFormat<LoginResponseApi>,LoginRequestApi>({
        path: '/Auth/login/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const { 
        handleLogin,
        userData,
        updateUserDate
    } =  useContext(AuthContext)


    const {
        updateNotificationOpen,
        updateMessage
      } = useContext(Toastification)
    
      useEffect(() => {
        const url = new URL(window.location.href)
        const searchParams = url.searchParams
        
        console.log('searchParams: ', searchParams.size)
        if(searchParams.size){

          const username = searchParams.get('username')
    
          setPassword(`${username}@espol.edu.ec`)
          setEmail(`${username}@espol.edu.ec`)
        } 
        else {
          window.location.href = "/Login/autenticar"
        }
        
        const token = localStorage.getItem('token')
        
        if (!token)
            return

        const userDataString = localStorage.getItem('UserData')

        if (!userDataString)
            return

        updateUserDate(JSON.parse(userDataString))
      // eslint-disable-next-line react-hooks/exhaustive-deps
      },[])
    
      useEffect(() => {
        const asyncAuth = async () => {
          if (email !== null && password!== null) {
      
            const payload: LoginRequestApi = {
              email: email?? '',
              password: password?? ''
            }
            try {
              const response = await requestLogin.submit(payload)
      
              console.log('response: ', response)
              if (!response)
                return
              
              const { data, succeeded, message } = response
              if(!succeeded){
                console.log(message)
                window.location.href = "/Login/autenticar"
                updateMessage(message)
                updateNotificationOpen(true)
              }if (!data)
                return 
              const { token } = data
              console.log('token: ', token)
              handleLogin(token)
              updateMessage('Se inicio sesion correctamente')
              updateNotificationOpen(true)
            }catch(err) {
              window.location.href = "/Login/autenticar"
              updateMessage('ERROR al completar el requerimiento')
              updateNotificationOpen(true)
            } 
            
          }
        }
        asyncAuth()
      // eslint-disable-next-line react-hooks/exhaustive-deps
      }, [email])

      useEffect(() => {
        if (userData){
            const userDataString = localStorage.getItem('UserData')
            if (!userDataString)
                return
            const { role } = JSON.parse(userDataString)
            console.log('userDat index: ', userDataString)
            switch (role) {
                case 'ADM':
                    window.location.href = "/Administrador/servicios"
                    break
                case 'CL':
                    window.location.href = "/Cliente/mis-solicitudes"
                    break
                case 'AS':
                    window.location.href = '/Asistente-Silab/solicitudes'
                    break
                case 'AD':
                    window.location.href = '/Asistente-Laboratorio/solicitudes'
                    break
                default:
                    window.location.href = "/Login/autenticar"
                    break
            }
        }
      }, [userData])

    return (
        <div>
          <Backdrop
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={true}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
        </div>
    )
}

export default Index