import { RouteObject } from 'react-router-dom'
import Error from '../../pages/Error'
import LaboratoryAssistant from '../../pages/LaboratoryAssistant/Index'
import RequestLaboratory from '../../pages/LaboratoryAssistant/Request'
import ViewRequest from '../../pages/LaboratoryAssistant/ViewRequest'

const LaboratoryAssistantRouter: RouteObject[] = [
    {
      path: "/Asistente-Laboratorio",
      element: <LaboratoryAssistant/>,
      errorElement: <Error/>,
      children: [
        {
          path: "solicitudes",      
          element: <RequestLaboratory/>,
          errorElement: <Error/>
        },
        {
          path: "solicitudes/visualizar/:id",      
          element: <ViewRequest/>,
          errorElement: <Error/>
        },
      ]
    }
]

export { LaboratoryAssistantRouter }