import { Avatar, Box, Button, Card, CardContent, Grid, Link, TextField, Typography } from '@mui/material'
import LockOutlinedIcon from '@mui/icons-material/LockOutlined'
import { themePalette } from '../../../config/MaterialUi/Theme.Object'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { initialValueLogin } from '../../../constants/FormConstants'
import { validationSchemaLogin } from '../../../constants/ValidationSchemas'
import { DataForm, valueFormLogin } from "../../../types/Form"
import { useContext, useState } from 'react'
import { AuthContext } from '../../../context/AuthContext'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { LoginRequestApi, LoginResponseApi } from '../../../types/API/Login'
import { Toastification } from '../../../context/ToastificationContext';


const LoginForm = () => {

  const [ isUserEspol, setIsUserEspol ] = useState<boolean>(false)
  const { submit } = useRequestApi<ResponseFormat<LoginResponseApi>,LoginRequestApi>({
    path: '/Auth/login/',
    url: process.env.REACT_APP_API_GATEWAY,
    method: MetodoHTTP.POST
  })

  const {
    updateNotificationOpen,
    updateMessage,
    updateTypeNotification
  } = useContext(Toastification)

  const { 
    handleLogin
  } =  useContext(AuthContext)

  const handleCheckboxChange = () => {
    setIsUserEspol(!isUserEspol);
  }


  const handleSubmit = async (dataForm: DataForm) => {
    // console.log('login: ' , dataForm)
    const dataFormAux = dataForm as valueFormLogin
    const { email, password } =  dataFormAux

    const payload: LoginRequestApi = {
      email,
      password
    }
    try {
        const response = await submit(payload)
      if (!response)
        return
      
      const { data,succeeded, message } = response
      
      if (!data)
        return 
      const { token } = data
      handleLogin(token)
      updateNotificationOpen(true)
      if ( succeeded ){
        updateMessage('Se inicio sesion correctamente')
        window.location.href = "/index"
      } else {
        updateTypeNotification(false)
        updateMessage(message)
      }
    } catch(ex) {
        updateNotificationOpen(true)
        updateTypeNotification(false)
        updateMessage("Correo o contrasea incorrecto")
    }
  }

  const {
    HandleTextFieldChange,
    handleTextFieldBlur,
    formik
  } = useFormValidateHook({
      initialValues: initialValueLogin,
      validationSchema: validationSchemaLogin,
      onSubmit: handleSubmit
  })
  
  return (
    <Card sx={{ borderWidth:'bold', borderRadius: 5, px: 1 }}>
      <CardContent>
          <Box
            sx={{
              my: 8,
              mx: 'auto',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
              <Avatar sx={{ m: 1, bgcolor: 'primary.main',  }}>
                <LockOutlinedIcon />
              </Avatar>
              <Typography component="h1" variant="h5" color={themePalette.PRIMARY}>
                  Iniciar Sesion
              </Typography>
              <form onSubmit={formik.handleSubmit}>
                  <Box 
                    sx={{
                      minWidth: "400px",
                      mt: 4,
                      px: 1
                    }}
                  >
                    <TextField
                      id="email"
                      label="Correo"
                      value={formik.values.email}
                      onChange={HandleTextFieldChange}
                      onBlur={handleTextFieldBlur}
                      error={formik.touched.email && Boolean(formik.errors.email)}
                      helperText={formik.touched.email && formik.errors.email}
                      variant="outlined"
                      fullWidth
                      InputProps={{
                          sx: {
                            borderRadius: '10px', // Aplica el radio de borde al TextField
                            '& fieldset': {
                              borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                            },
                          },
                      }}
                      InputLabelProps={{
                          sx: {
                            color: themePalette.PRIMARY, // Cambia el color del label a azul
                          },
                      }}
                    />
                  </Box>
                  <Box 
                    sx={{
                      minWidth: "400px",
                      mt: 4,
                      px: 1
                    }}
                  >
                    <TextField
                      id="password"
                      label="Contrasea"
                      type='password'
                      value={formik.values.password}
                      onChange={HandleTextFieldChange}
                      onBlur={handleTextFieldBlur}
                      error={formik.touched.password && Boolean(formik.errors.password)}
                      helperText={formik.touched.password && formik.errors.password}
                      variant="outlined"
                      fullWidth
                      InputProps={{
                          sx: {
                            borderRadius: '10px', // Aplica el radio de borde al TextField
                            '& fieldset': {
                              borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                            },
                          },
                      }}
                      InputLabelProps={{
                          sx: {
                            color: themePalette.PRIMARY, // Cambia el color del label a azul
                          },
                      }}
                    />
                  </Box>
                    <Box
                      sx={{
                        minWidth: "400px",
                        mt: 2,
                        px: 1
                      }}
                    >
                      <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 1, mb: 2 }}
                      >
                        Iniciar Sesin 
                      </Button>
                    </Box>
                    <Box
                      sx={{
                        minWidth: "400px",
                        mt: 2,
                        px: 1
                      }}
                    >
                      <Grid container>
                        <Grid item xs>
                            <Link href={`${process.env.REACT_APP_API_EXTERNAL_URL}ExternalAuth/espol/login`} variant="body2" sx={{ textDecoration: 'none'}}>Usuario espol</Link>
                        </Grid>
                        <Grid item>

                            <Link href="/Login/registrar" variant="body2" sx={{ textDecoration: 'none'}}>
                                {"Registrate."}
                            </Link>
                        </Grid>
                      </Grid>
                  </Box>
              </form>
          </Box>
      </CardContent>
    </Card>
  )
}

export default LoginForm