import { createContext, useState } from "react"

interface ContexAuthtProps {
    handleLogout: () => void,
    handleLogin: (token: string) => void,
    setIsLoggedIn: (value: boolean) => void,
    setToken: (value: string | null) => void,
    isLoggedIn: boolean,
    token: string | null,
    userData: any | null,
    updateUserDate: (value: any)=> void
}

export const AuthContext = createContext<ContexAuthtProps>({} as ContexAuthtProps) 

export const AuthProvider = ({children}: any) => {

    const [isLoggedIn, setIsLoggedIn] = useState(false)
    const [token, setToken] = useState<string | null>(null)
    const [userData, setUserData] = useState<any | null>(null)    
    
    const updateUserDate = (value: any) => {
        setUserData(value)
    }

    
    const handleLogout = () => {
        localStorage.removeItem('token')
        localStorage.removeItem('UserData')
        setIsLoggedIn(false)
        setToken(null)
        setUserData(null)
    }


    const handleLogin = async (token: string) => {
        localStorage.setItem('token', token)
        setIsLoggedIn(true)
        setToken(token)
        const payloadBase64 = token.split('.')[1]
        const decodedPayload = atob(payloadBase64)
        const userDataDecode = JSON.parse(decodedPayload)
        localStorage.setItem('UserData', JSON.stringify(userDataDecode, null, 2))
        setUserData(userDataDecode)
    }

    return (
       <AuthContext.Provider
            value={{
                handleLogout,
                handleLogin,
                setIsLoggedIn,
                setToken,
                isLoggedIn,
                token,
                userData,
                updateUserDate
            }}
       >
            {children}
       </AuthContext.Provider> 
    )
}