import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from "react-router-dom"
import { Autocomplete, Box, Button, Checkbox, FormControl, FormControlLabel, Grid, Switch, TextField } from '@mui/material'
import { CreateStandardServiceRequestApi, CreateStandardServiceResponseApi, GetStandardServiceResponseApi, UpdateStandardServiceRequestApi } from '../../../types/API/ServiceStandardization'
import { DataForm, valueFormStandardizationService } from '../../../types/Form'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { initialValueStandardizationService } from '../../../constants/FormConstants'
import { validationSchemaServiceStandardizationProcesses } from '../../../constants/ValidationSchemas'
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { GetLaboratoriesRequestApi, GetLaboratoriesResponse, GetLaboratoriesResponseApi, Service } from '../../../types/API/Laboratories'
import { ResponseFormat } from '../../../types/API/Index'
import { GetServicesResponse, MetodologiaResponse, MuestraResponse, ParametrosResponse } from '../../../types/API/Services'
import { themePalette } from '../../../config/MaterialUi/Theme.Object'
import { Toastification } from '../../../context/ToastificationContext'

type AddServiceStandardizationsProps = {
    idStandardizationService: string | undefined,
    currentStandardizationService?: GetStandardServiceResponseApi
}

const AddStandardizationServiceForm: React.FC<AddServiceStandardizationsProps> = ({currentStandardizationService, idStandardizationService}) => {
    
    const [searchLaboratory, setSearchLaboratory] = useState<string|undefined>(undefined)
    const [optionsLaboratories, setOptionsLaboratories] = useState<GetLaboratoriesResponse[]>([])
    const [optionsServices, setOptionsServices] = useState<Service[]>([])
    const [optionsParams, setOptionsParams] = useState<ParametrosResponse[]>([])
    const [optionsMethologies, setOptionsMethologies] = useState<MetodologiaResponse[]>([])
    const [optionsSamples, setOptionsSamples] = useState<MuestraResponse[]>([])
    
    const [currentLaboratory, setCurrentLaboratory] = useState<GetLaboratoriesResponse>()
    const [currentService, setCurrentService] = useState<Service>()
    const [currentParams, setCurrentParams] = useState<ParametrosResponse>()
    const [currentMethology, setCurrentMethology] = useState<MetodologiaResponse>()
    const [currentSample, setCurrentSample] = useState<MuestraResponse>()
    const [currentAcreditado, setCurrentAcreditado] = useState<boolean>(false)
    const [currentMuestreo, setCurrentMuestreo] = useState<boolean>(false)
    const [estimatedTime, setEstimatedTime] = useState<string>('')
    const [estimatedCost, setEstimatedCost] = useState<string>('')

    const navigate = useNavigate();

    const requestLaboratories = useRequestApi<ResponseFormat<GetLaboratoriesResponseApi>,GetLaboratoriesRequestApi>({
        path: '/Laboratorio/GetLaboratories',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestService = useRequestApi<ResponseFormat<GetServicesResponse>,string>({
        path: `/Servicio/GetService/${currentService?.idservservicio}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const requestServiceStandardizationCreate = useRequestApi<ResponseFormat<CreateStandardServiceResponseApi>,CreateStandardServiceRequestApi>({
        path: `/ServicioEstandarizado/CreateStandardService`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestServiceStandardizationUpdate = useRequestApi<ResponseFormat<UpdateStandardServiceRequestApi>,UpdateStandardServiceRequestApi>({
        path: `/ServicioEstandarizado/UpdateStandardService/${idStandardizationService}`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.PUT
    })

    const {
        updateNotificationOpen,
        updateMessage,
        updateTypeNotification
      } = useContext(Toastification)

    const submitEdit = async (dataForm: valueFormStandardizationService) => {
        console.log('Editar: ', dataForm)
        const payload: UpdateStandardServiceRequestApi = {
            idservinfoservdepartamento: Number(idStandardizationService) ?? 0,
            acreditado: dataForm.accredited,
            muestreo: dataForm.sampling,
            laboratorioId: dataForm.laboratory?.laboratorio?.idlablaboratorios ?? 0,
            metodologiaId: dataForm.methology?.idservmetodologia ?? 0,
            muestraId: dataForm.sample?.idservmuestra ?? 0,
            parametroServicioId: dataForm.param?.idservparametroservicio ?? 0,
            servicioId: dataForm.service?.idservservicio ?? 0,
            tipoAcreditacion: '',
            costo: Number(dataForm.estimatedCost)??0,
            tiempoEstimado: Number(dataForm.estimatedTime)??0
        }

        console.log('edit: ', payload)
        const response = await requestServiceStandardizationUpdate.submit(payload)

        if ( !response )
            return
        
        const { message, succeeded } = response

        if ( succeeded ){
            updateMessage(message)
            updateNotificationOpen(true)
            setTimeout(() => {
                updateTypeNotification(true)
                updateNotificationOpen(false)
                window.location.href = '/Administrador/estandarizacionNew/'
            } ,1000)
        } else {
            updateMessage(message)
            updateTypeNotification(false)
            updateNotificationOpen(true)
        }
        
        
    }

    const handleSubmit = async (dataForm: DataForm) => { 
        const dataAux = dataForm as valueFormStandardizationService

        if ( idStandardizationService ) {
            submitEdit(dataAux)
            return
        }

        const payload: CreateStandardServiceRequestApi = {
            acreditado: dataAux.accredited,
            muestreo: dataAux.sampling,
            laboratorioId: dataAux.laboratory?.laboratorio?.idlablaboratorios ?? 0,
            metodologiaId: dataAux.methology?.idservmetodologia ?? 0,
            muestraId: dataAux.sample?.idservmuestra ?? 0,
            parametroId: dataAux.param?.idservparametroservicio ?? 0,
            servicioId: dataAux.service?.idservservicio ?? 0,
            tipoAcreditacion: '',
            costo: Number(dataAux.estimatedCost) ?? 0,
            tiempoEstimado: Number(dataAux.estimatedTime) ?? 0
        }

        const response = await requestServiceStandardizationCreate.submit(payload)

        if ( !response )
            return

        const { succeeded, message } = response

        if ( succeeded ) {
            updateMessage(message)
            updateNotificationOpen(true)
            setTimeout(() => {
                updateTypeNotification(true)
                updateNotificationOpen(false)
                window.location.href = '/Administrador/estandarizacionNew/'
            } ,1000)
        } else {
            updateMessage(message)
            updateTypeNotification(false)
            updateNotificationOpen(true)
        }
    }

    useEffect(() => {
        const initializateOptionsValue = async () => {
            const response = await requestService.submit('')

            if (!response) 
                return
            
            const { data } = response

            if (!data) 
                return

            const { metodologias, muestras, parametros} = data
            setOptionsParams(parametros)
            setOptionsMethologies(metodologias)
            setOptionsSamples(muestras)
        }

        if ( currentService )
            initializateOptionsValue()
    }, [currentService])

    useEffect(() => {
        if ( idStandardizationService && currentStandardizationService) {
            handleOtherFieldChange(currentMuestreo,"sampling")
            handleOtherFieldChange(currentAcreditado,"accredited")
            handleOtherFieldChange(currentMethology,"methology")
            handleOtherFieldChange(currentParams,"param")
            handleOtherFieldChange(currentSample,"sample")
        }
    },[currentParams,currentSample,currentMethology,currentMuestreo,currentAcreditado])

    useEffect(() => {
        if ( 
            idStandardizationService && 
            currentStandardizationService &&
            currentService &&
            optionsParams.length &&
            optionsMethologies.length &&
            optionsSamples.length
        ) {
            const { departamento } = currentStandardizationService
            const { metodologiaId, muestraId, parametroId, muestreo, acreditado, tiempoEstimado, costo } = departamento

            const auxCurrentParam = optionsParams.find( param => param.idservparametroservicio === parametroId)
            const auxCurrentSample = optionsSamples.find( sample => sample.idservmuestra === muestraId)
            const auxCurrentMethology = optionsMethologies.find( mathology => mathology.idservmetodologia === metodologiaId)

            setCurrentParams(auxCurrentParam)
            setCurrentMethology(auxCurrentMethology)
            setCurrentSample(auxCurrentSample)
            setCurrentMuestreo(muestreo)
            setCurrentAcreditado(acreditado)
            setEstimatedTime(`${tiempoEstimado}`)
            setEstimatedCost(`${costo}`)
            // handleOtherFieldChange(auxCurrentParam,"param")
            // handleOtherFieldChange(auxCurrentSample,"sample")
            // handleOtherFieldChange(auxCurrentMethology,"methology")
            // handleOtherFieldChange(muestreo,"sampling")
            // handleOtherFieldChange(acreditado,"accredited")
            handleOtherFieldChange(`${tiempoEstimado}`,"estimatedTime")
            handleOtherFieldChange(`${costo}`,"estimatedCost")
        }
    }, [currentService, idStandardizationService, currentStandardizationService,optionsParams, optionsMethologies, optionsSamples])

    useEffect(() => {
        if ( idStandardizationService && currentStandardizationService && optionsServices.length) {
            const { departamento } = currentStandardizationService
            const { servicioId } = departamento

            const auxCurrentService = optionsServices.find( service => service.idservservicio === servicioId) 
            setCurrentService(auxCurrentService)
            handleOtherFieldChange(auxCurrentService,"service")
        }
    }, [optionsServices, idStandardizationService, currentStandardizationService])

    useEffect(() => {
        if( idStandardizationService && currentStandardizationService ){
            console.log('entre a este if de este use effect')
            const { laboratorio } = currentStandardizationService           
            const auxCurrentLaboratory = optionsLaboratories.find( laboratory => laboratory.laboratorio.idlablaboratorios === laboratorio.idlablaboratorios)
            setCurrentLaboratory(auxCurrentLaboratory)
            handleOtherFieldChange(auxCurrentLaboratory,"laboratory")
            setOptionsServices(auxCurrentLaboratory?.servicios??[])

        }
    },[optionsLaboratories,idStandardizationService,currentStandardizationService])

    useEffect(() => {
        const initializateOptionsValues = async () => {
            const payload: GetLaboratoriesRequestApi = {
                search: searchLaboratory??'',
                pageNumber: 1,
                pageSize: 10,
                orderById: false,
                orderByName: false,
                orderByStatus: false,
                orderByDescription: false,
                onlyActive: false,
            }
            const response = await requestLaboratories.submit(payload)

            if (!response)
                return

            const { data } = response
            if ( !data ) 
                return

            const { items } = data

            setOptionsLaboratories(items)

        }

        initializateOptionsValues()
    }, [searchLaboratory])

    useEffect(() => {
        setSearchLaboratory('')
    }, [])

    const {
        handleTextFieldBlur,
        handleOtherFieldChange,
        HandleTextFieldChange,
        formik
    } = useFormValidateHook({
        initialValues: initialValueStandardizationService,
        validationSchema: validationSchemaServiceStandardizationProcesses,
        onSubmit: handleSubmit
    }) 

    return (
        <form onSubmit={formik.handleSubmit}>
            <Grid container spacing={2}>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete 
                            disablePortal
                            id="laboratory"
                            options={optionsLaboratories}
                            getOptionLabel={(option) => option.laboratorio.nombre}
                            value={
                                    currentLaboratory 
                                        ? optionsLaboratories.find( 
                                            lab => lab.laboratorio.idlablaboratorios === currentLaboratory.laboratorio.idlablaboratorios
                                            ) 
                                        : null
                                } 
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="1. Seleccione un Laboratorio"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.laboratory && Boolean(formik.errors.laboratory)}
                                    helperText={formik.touched.laboratory && formik.errors.laboratory}
                                />
                            )}
                            onChange={(event, newInputValue: GetLaboratoriesResponse | null) => {
                                if (newInputValue){
                                    setCurrentLaboratory(newInputValue)
                                    setOptionsServices([])
                                    setOptionsParams([])
                                    setOptionsMethologies([])
                                    setOptionsSamples([])
                                    setOptionsServices(newInputValue?.servicios)
                                    setCurrentService(undefined)
                                    setCurrentParams(undefined)
                                    setCurrentMethology(undefined)
                                    setCurrentSample(undefined)
                                } else {
                                    setCurrentLaboratory(undefined)
                                }
                                handleOtherFieldChange(newInputValue,"laboratory")
                            }}
                            isOptionEqualToValue={(option, value) => option.laboratorio?.idlablaboratorios === value.laboratorio?.idlablaboratorios}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="service"
                            options={optionsServices}
                            getOptionLabel={(option) => option.nombre}
                            value={
                                currentService
                                    ? optionsServices.find( serv => (
                                        serv.idservservicio === currentService.idservservicio
                                    ))
                                    : null
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="2. Seleccione un Servicio"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.service && Boolean(formik.errors.service)}
                                    helperText={formik.touched.service && formik.errors.service}
                                />
                            )}
                            onChange={(event, newInputValue: Service | null) => {
                                if (newInputValue){
                                    setCurrentService(newInputValue)
                                    setOptionsParams([])
                                    setOptionsMethologies([])
                                    setOptionsSamples([])
                                    setCurrentParams(undefined)
                                    setCurrentMethology(undefined)
                                    setCurrentSample(undefined)
                                }else{
                                    setCurrentService(undefined)
                                }
                                handleOtherFieldChange(newInputValue,"service")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservservicio === value.idservservicio}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="param"
                            options={optionsParams}
                            getOptionLabel={(option) => option.nombre}
                            value={
                                currentParams
                                    ? optionsParams.find( par => (
                                        par.idservparametroservicio === currentParams.idservparametroservicio
                                    ))
                                    : null
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="3. Seleccione un Parametro"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.param && Boolean(formik.errors.param)}
                                    helperText={formik.touched.param && formik.errors.param}
                                />
                            )}
                            onChange={(event, newInputValue: ParametrosResponse | null) => {
                                if (newInputValue){
                                    setCurrentParams(newInputValue)
                                } else {
                                    setCurrentParams(undefined)
                                }
                                handleOtherFieldChange(newInputValue,"param")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservparametroservicio === value.idservparametroservicio}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="methology"
                            options={optionsMethologies}
                            getOptionLabel={(option) => option.nombre}
                            value={
                                currentMethology
                                    ? optionsMethologies.find( meth => (
                                        meth.idservmetodologia === currentMethology?.idservmetodologia
                                    ))
                                    : null
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="4. Seleccione una Metodologa"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.methology && Boolean(formik.errors.methology)}
                                    helperText={formik.touched.methology && formik.errors.methology}
                                />
                            )}
                            onChange={(event, newInputValue: MetodologiaResponse | null) => {
                                if (newInputValue){
                                    setCurrentMethology(newInputValue)
                                } else {
                                    setCurrentMethology(undefined)
                                }
                                handleOtherFieldChange(newInputValue,"methology")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservmetodologia === value.idservmetodologia}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 2,
                            px: 1,
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="sample"
                            options={optionsSamples}
                            getOptionLabel={(option) => option.nombre}
                            value={
                                currentSample
                                    ? optionsSamples.find( sam => (
                                        sam.idservmuestra === currentSample?.idservmuestra
                                    ))
                                    : null
                            }
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="5. Seleccione una muestra"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.sample && Boolean(formik.errors.sample)}
                                    helperText={formik.touched.sample && formik.errors.sample}
                                />
                            )}
                            onChange={(event, newInputValue: MuestraResponse | null) => {
                                if (newInputValue){
                                    setCurrentSample(newInputValue)
                                } else {
                                    setCurrentSample(undefined)
                                }
                                handleOtherFieldChange(newInputValue,"sample")
                            }}
                            isOptionEqualToValue={(option, value) => option.idservmuestra === value.idservmuestra}
                            fullWidth
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 3,
                            px: 1,
                        }}
                    >
                        <FormControl 
                            sx={{ marginRight: 2 }}
                        >
                            <FormControlLabel
                                control={<Switch />}
                                label="Acreditado"
                                checked={currentAcreditado}
                                onChange={(event, newInputValue: boolean) => {
                                    handleOtherFieldChange(newInputValue,"accredited")
                                    setCurrentAcreditado(newInputValue)
                                }} 
                            />
                        </FormControl>
                        <FormControl>
                            <FormControlLabel 
                                control={<Switch  />}
                                label="Realiza muestreo"
                                checked={currentMuestreo}
                                onChange={(event, newInputValue: boolean) => {
                                    handleOtherFieldChange(newInputValue,"sampling")
                                    setCurrentMuestreo(newInputValue)
                                }} // Funcin
                            />
                        </FormControl>
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 3,
                            px: 1,
                        }}
                    >
                        <TextField
                            id="estimatedTime"
                            label="Tiempo estimado del requerimiento (en das)"
                            color="primary"
                            type='text'
                            value={formik.values.estimatedTime}
                            onChange={HandleTextFieldChange}
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.estimatedTime && Boolean(formik.errors.estimatedTime)}
                            helperText={formik.touched.estimatedTime && formik.errors.estimatedTime}
                            variant="outlined"
                            fullWidth
                            InputProps={{
                                sx: {
                                  borderRadius: '5px', // Aplica el radio de borde al TextField
                                  '& fieldset': {
                                    borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                  },
                                },
                            }}
                            InputLabelProps={{
                                sx: {
                                  color: themePalette.PRIMARY, // Cambia el color del label a azul
                                },
                            }}
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={6}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 3,
                            px: 1,
                        }}
                    >
                        <TextField
                            id="estimatedCost"
                            label="Costo estimado"
                            placeholder='$0.00'
                            color="primary"
                            type='text'
                            value={formik.values.estimatedCost}
                            onChange={HandleTextFieldChange}
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.estimatedCost && Boolean(formik.errors.estimatedCost)}
                            helperText={formik.touched.estimatedCost && formik.errors.estimatedCost}
                            variant="outlined"
                            fullWidth
                            InputProps={{
                                sx: {
                                  borderRadius: '5px', // Aplica el radio de borde al TextField
                                  '& fieldset': {
                                    borderColor: themePalette.PRIMARY, // Cambia el color del borde a rojo
                                  },
                                },
                            }}
                            InputLabelProps={{
                                sx: {
                                  color: themePalette.PRIMARY, // Cambia el color del label a azul
                                },
                            }}
                        />
                    </Box>
                </Grid>
                <Grid
                    item
                    md={12}
                    xs={12}
                >
                    <Box 
                        sx={{
                            width: "100%",
                            mt: 3,
                            px: 1,
                        }}
                    >
                        <Button 
                            type="submit"
                            variant="contained"
                            color="primary"
                            sx={{ 
                                mt: 2,
                                height: "60px",
                                width: '100%'
                            }}
                        >
                            Guardar
                        </Button>
                    </Box>
                </Grid>
            </Grid>
        </form>
    )
}

export default AddStandardizationServiceForm