import { useContext, useEffect, useState } from "react"
import ModalAdd from "./ModalAdd"
import { Button, Typography } from '@mui/material'
import { DataTableContext } from "../../../context/DataTableContext"
import { RowMuestra } from "../../../types/DataTable"
import useRequestApi, { MetodoHTTP } from "../../../hooks/useRequestApi"
import { ResponseFormat } from "../../../types/API/Index"
import { DeleteSampleRequestApi, DeleteSampleResponseApi } from "../../../types/API/Sample"
import { Toastification } from "../../../context/ToastificationContext"

type DeleteSampleProps = {
    showDelete: boolean,
    setShowDelete: (value: boolean) => void
}

const DeleteMethology: React.FC<DeleteSampleProps> = ({ showDelete, setShowDelete }) => {
    const [title, setTitle] = useState<string>('Desactivar')
    const [isDelete, seIsDelete] = useState<boolean>(true)
    
    const { 
        currentRowSelected,
        setDataContext,
        dataAux
    } = useContext(DataTableContext)

    const {
        updateNotificationOpen,
        updateMessage
    } = useContext(Toastification)


    useEffect(() => {
        const currentRow = currentRowSelected as RowMuestra
        if (currentRow?.estado === 'INACTIVO') {
            setTitle('Activar')
            seIsDelete(false)
        } else{
            setTitle('Inactivar')
            seIsDelete(true)
        }
    }, [currentRowSelected])

    const { submit } = useRequestApi<ResponseFormat<DeleteSampleResponseApi>,DeleteSampleRequestApi>({
        path: `/Recurso/DeleteSample/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const handleDelete =async () => {
        const currentRow = currentRowSelected as RowMuestra
        const response = await submit({
            id: currentRow.id,
            delete: isDelete
        })

        if (!response)
            return
        const { succeeded, message } = response
        
        if (!succeeded)
            return
        updateMessage(message)
        updateNotificationOpen(true)
    }
  
    return (
        <ModalAdd
            show={showDelete}
            setShow={setShowDelete}
        >
            <h1>{title} Muestra</h1>
            <Typography
                variant='body1'
            >
                Esta seguro de {title} esta Muestra
            </Typography>
            <Button
                type='button'
                variant='contained'
                color={isDelete ? 'error' : 'primary'}
                onClick={handleDelete}
            >
                {title}
            </Button>
        </ModalAdd>
    )
}

export default DeleteMethology