import {  Box, Grid, Paper } from '@mui/material'
import { themePalette } from '../../config/MaterialUi/Theme.Object'
import { Outlet } from 'react-router-dom'

const Index = () => {
  return (
    <Grid
        container
        component="main"
        sx={{
            height: '100vh'
        }}
    >
        <Grid
            item
            sm={4}
            xs={false}
            md={7}
            sx={{
                backgroundImage: `url('/images/SILAB IMAGE.png')`,
                backgroundRepeat: 'no-repeat',
                backgroundColor: (t) => t.palette.mode === 'light' ? t.palette.grey[50] : t.palette.grey[900],
                backgroundSize: '100% 100%',
                backgroundPosition: 'center',
            }}
        >
        </Grid>
        <Grid
            item
            xs={12}
            sm={8}
            md={5}
            component={Paper}
            elevation={2}
            sx={{
                backgroundColor: themePalette.PRIMARY,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                py: 1,
                my:0
            }}
            square
        >
            <Box
                sx={{
                    minWidth: '500px',
                    my: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Outlet/>
                {/* <LoginForm/> */}
                {/* <RegisterForm/> */}
            </Box>
        </Grid>
    </Grid>
  )
}

export default Index