import { Card, CardContent, useMediaQuery, useTheme } from '@mui/material'
import { useState } from 'react'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import Box from '@mui/material/Box'
import TabMethodology from './components/TabMethodology'
import TabSample from './components/TabSample'
import { themePalette } from '../../config/MaterialUi/Theme.Object'


interface TabPanelProps {
  children?: React.ReactNode,
  index: number,
  value: number
}

function CustomTabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  )
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}


const Resource: React.FC = () => {
  const [value, setValue] = useState(0)
  const theme = useTheme()
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'))

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Box 
        sx={{ 
          borderBottom: 1, 
          borderColor: themePalette.BG,
          px: 3,
          mt: !isSmallScreen ? '' : 6,
        }}
      >
        <Card sx={{ borderWidth:'bold', borderRadius: 2, boxShadow: 'none' }} >
          <CardContent>
            <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
              <Tab label="Metodologia" {...a11yProps(0)} />
              <Tab label="Muestra" {...a11yProps(1)} />
            </Tabs>
          </CardContent>
        </Card>
      </Box>
      <CustomTabPanel value={value} index={0}>
        <TabMethodology/>
      </CustomTabPanel>
      <CustomTabPanel value={value} index={1}>
        <TabSample/>
      </CustomTabPanel>
    </Box>
  )
}

export default Resource