import { useEffect, useState } from 'react'
import { useTheme } from '@mui/material/styles'
import List from '@mui/material/List'
import IconButton from '@mui/material/IconButton'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import { Drawer, DrawerHeader } from '../utils/components/Drawer'
import { themePalette } from '../config/MaterialUi/Theme.Object'
import { optionMenu } from '../types/optionMenueSidebar'
import { Link } from 'react-router-dom'
import { Box } from '@mui/material'
import { UserData } from '../types/DataUser'

type SideBarProps = {
  open: boolean,
  options: optionMenu[],
  setOpen: (value: boolean) => void
}

const SideBar: React.FC<SideBarProps> = ({open, setOpen, options}) => {
  const [userData, setuserData] = useState<UserData>()

  useEffect(() => {
    const userDataString = localStorage.getItem('UserData')
      if (!userDataString)
        return
    const jsonUserData = JSON.parse(userDataString)
    
    setuserData(jsonUserData as UserData)
  }, [])
  

  const theme = useTheme()


  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Drawer
      variant="permanent"
      open={open}
    >
      <DrawerHeader 
        background={themePalette.PRIMARY}
      >
        <IconButton onClick={handleDrawerClose} sx={{ color: "#FFF" }}>
        {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
        </IconButton>
      </DrawerHeader>
      <List
        sx={{
          background: themePalette.PRIMARY,
          height: "100%"
        }}
      >
        {(options.filter((option: optionMenu) => option.roles[0] === userData?.role)).map((option: optionMenu, index: number) => 
        (<ListItem key={index} disablePadding sx={{ display: 'block' }}>
          <Box
            sx={{
              minHeight: '80px' 
            }}
          >
            <Link to={option.path} style={{ textDecoration: 'none' }}>
              <ListItemButton
                sx={{
                  minHeight: 48,
                  justifyContent: open ? 'initial' : 'center',
                  px: 2.5,
                }}
              >
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: open ? 3 : 'auto',
                    mx: 5,
                    justifyContent: 'center',
                    color: "#FFF"
                  }}
                >
                  <option.icon/>
                </ListItemIcon>
                <ListItemText
                    primary={option.name}
                    sx={{ 
                      opacity: open ? 1 : 0,
                      textOverflow: 'ellipsis',
                      color: "#FFF"
                    }}
                />
              </ListItemButton>
            </Link>
          </Box>          
        </ListItem>
        ))}
      </List>
    </Drawer>
  )
}

export default SideBar
