import { RouteObject } from 'react-router-dom'
import Error from '../pages/Error'
import Index from '../pages/Index'

const MainRouter: RouteObject[] = [
    {
      path: "/",
      element: <Index/>,
      errorElement: <Index/>,
    },
    {
      path: "/index",
      element: <Index/>,
      errorElement: <Error/>,
    }
]

export { MainRouter}