
import { useContext, useEffect } from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import { adminRouter } from './Admin/Index'
import { loginRouter } from './Login/Index'
import { MainRouter } from './MainRouter'
import { clientRouter } from './Client/Index'
import { AuthContext } from '../context/AuthContext'
import { UserData } from '../types/DataUser';
import { SilabAssistantRouter } from './SilabAssistant/Index'
import { LaboratoryAssistantRouter } from './LaboratoryAssistant/Index'

const Router = () => {
  
  const { 
    handleLogin,
    userData
  } =  useContext(AuthContext)

  useEffect(() => {
    const storedToken = localStorage.getItem('token') 
    if (storedToken) {
      handleLogin(storedToken)
    }
  },[])


  console.log('userData: ', userData)
  console.log('router: ', LaboratoryAssistantRouter)

  return (
    <BrowserRouter>
      <Routes>
        {MainRouter.map((route) => (
          <Route
            key={route.path}
            path={route.path}
            element={route.element}
            caseSensitive={false}
          />
        ))}
        {loginRouter.map((route) => (
          <Route
            key={route.path}
            path={route.path}
            element={route.element}
            caseSensitive={false}
          >
            {route.children && route.children.map((childRoute) => (
              <Route
                key={childRoute.path}
                path={childRoute.path}
                element={childRoute.element}
                caseSensitive={false} 
              />
            ))}
          </Route>
        ))}
        {
          ((userData as UserData)?.role??'') === 'ADM' 
            && adminRouter.map((route) => (
              <Route
                key={route.path}
                path={route.path}
                element={route.element}
                caseSensitive={false}
              >
                {route.children && route.children.map((childRoute) => (
                  <Route
                    key={childRoute.path}
                    path={childRoute.path}
                    element={childRoute.element}
                    caseSensitive={false} 
                  />
                ))}
              </Route>
            ))
        }
        {
          ((userData as UserData)?.role??'') === 'CL' 
            && clientRouter.map((route) => (
              <Route
                key={route.path}
                path={route.path}
                element={route.element}
                caseSensitive={false}
              >
                {route.children && route.children.map((childRoute) => (
                  <Route
                    key={childRoute.path}
                    path={childRoute.path}
                    element={childRoute.element}
                    caseSensitive={false} 
                  />
                ))}
              </Route>
            ))
        }
        {
          ((userData as UserData)?.role??'') === 'AS' 
            && SilabAssistantRouter.map((route) => (
              <Route
                key={route.path}
                path={route.path}
                element={route.element}
                caseSensitive={false}
              >
                {route.children && route.children.map((childRoute) => (
                  <Route
                    key={childRoute.path}
                    path={childRoute.path}
                    element={childRoute.element}
                    caseSensitive={false} 
                  />
                ))}
              </Route>
            ))
        }
        {
          ((userData as UserData)?.role??'') === 'AD' 
          && LaboratoryAssistantRouter.map((route) => (
            <Route
              key={route.path}
              path={route.path}
              element={route.element}
              caseSensitive={false}
            >
              {route.children && route.children.map((childRoute) => (
                <Route
                  key={childRoute.path}
                  path={childRoute.path}
                  element={childRoute.element}
                  caseSensitive={false} 
                />
              ))}
            </Route>
          ))
        }
      </Routes>
    </BrowserRouter>
  )
}

export default Router
