/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react"
import { Box, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent } from "@mui/material"
import { Column, RowRequest, TypesRowsDataTableContext, fetchDataFunc } from "../../types/DataTable"
import { RequestColumnsTable } from "../../constants/DataTableAtributtesConstants"
import DataTable from "../../components/DataTable/DataTable"
import useRequestApi, { MetodoHTTP } from "../../hooks/useRequestApi"
import { ResponseFormat } from "../../types/API/Index"
import { GetRequestsPagedRequestApi, GetRequestsPagedResponseApi, GetAllStatusResponseApi, Estado } from '../../types/API/Request';
import { UserData } from "../../types/DataUser"
import { useNavigate } from "react-router-dom"
import { formatDate } from "../../helpers/formatDate"


const RequestLaboratory = () => {
    const [userData, setuserData] = useState<UserData>()
    const [ columns, setColumns ] = useState<Column[]>([])
    const [showDelete, setShowDelete] = useState<boolean>(false)
    const [statusOptions, setStatusOptions] = useState<Estado[]>()
    const [status, setStatus] = useState<string>('')

    const navigate = useNavigate()
    
    const requestGetAllRequest = useRequestApi<ResponseFormat<GetRequestsPagedResponseApi>,GetRequestsPagedRequestApi>({
        path: '/Solicitud/GetRequestsPaged/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestGetAllStatus = useRequestApi<ResponseFormat<GetAllStatusResponseApi>,string>({
        path: `/Solicitud/GetAllStatus/`,
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.GET
    })

    const fetchData: fetchDataFunc<RowRequest> = async (
        page = 1,
        rowsPerPage = 5,
        searchValue = '',
        sortColumn = '',
        onlyActivate = false,
        statusId = Number(status),
    ) => {

        const payload: GetRequestsPagedRequestApi = {
            search: searchValue,
            pageNumber: page,
            pageSize: rowsPerPage,
            orderById: true,
            orderByName: false,
            orderByStatus: false,
            orderByDescription: false,
            role: 'AD',
            id: userData?.idlaboratorio??0,
            statusId: Number(statusId)
        }

        let totalRows = 0
        let rows: RowRequest[] = []

        const response = await requestGetAllRequest.submit(payload)

        if ( response ) {
            const { data } = response
            if ( data ){
                const { items, totalCount } = data
                rows  = items.map<RowRequest>(request => {
                    const { descripcion, idsolsolicitud: id, laboratorio, createdAt } = request
                    const fecha = formatDate(createdAt)
                    console.log(request.estado)
                    let row: RowRequest = {
                        descripcion,
                        id,
                        estado: (statusOptions?.find( statu => statu.idsolestado === request.estadoId))?.nombre ?? '',
                        laboratorio,
                        fecha
                    }
                    return row
                })
                totalRows = totalCount
            }
        }
        return {
            data: rows,
            totalRows
        }
    }

    const handleModalDelete = () => {
        setShowDelete(true)
    }

    const handlePageEdit = (currentRowSelected: TypesRowsDataTableContext | undefined) => {
        const currentService: RowRequest = currentRowSelected as RowRequest
        navigate(`visualizar/${currentService.id}`)
    }

    useEffect(() => {
        const userDataString = localStorage.getItem('UserData')
        if (!userDataString)
            return
        const jsonUserData = JSON.parse(userDataString)
        
        setuserData(jsonUserData as UserData)
    }, [])

    useEffect(() => {
        const getAsyncAllState = async () => {
            console.log('entre')
            const response = await requestGetAllStatus.submit('')
            
            if(!response)
                return
            
            const { data } = response

            if (!data)
                return
            
            setStatusOptions(data)
        }
        setColumns(RequestColumnsTable)
        getAsyncAllState()
    }, [userData])

    return (
        <Container maxWidth="xl">
            <Box
                sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between'
                }}
            >
                
                <DataTable<RowRequest>
                    columns={columns}
                    fetchData={fetchData}
                    setShowDelete={handleModalDelete}
                    handlePageEdit={handlePageEdit}
                    estadoId={Number(status)}
                >
                    <FormControl sx={{ width: 200 }}>
                        <InputLabel id="estado">Estado</InputLabel>
                        <Select
                            labelId="estado"
                            id="estado"
                            value={status+''}
                            label="Estado"
                            onChange={(event: SelectChangeEvent) => {
                                setStatus(event.target.value)
                            }}
                        >
                            {
                                statusOptions?.map( (obj, key) => (
                                    <MenuItem
                                        key={key}
                                        value={obj.idsolestado}
                                    >
                                        {obj.nombre}
                                    </MenuItem>
                                ))
                            }
                        </Select>
                    </FormControl>
                </DataTable>
            </Box>
        </Container>
    )
}

export default RequestLaboratory