import { useState, MouseEvent, useContext } from 'react'
import Toolbar from '@mui/material/Toolbar'
import Typography from '@mui/material/Typography'
import IconButton from '@mui/material/IconButton'
import MenuIcon from '@mui/icons-material/Menu'
import { Menu, MenuItem, useMediaQuery, useTheme, Box, Link } from '@mui/material'
import AccountCircle from '@mui/icons-material/AccountCircle'
import { AppBar } from '../utils/components/AppBar'
import { themePalette } from '../config/MaterialUi/Theme.Object'
import { AuthContext } from '../context/AuthContext'

type NavBarProps = {
  open: boolean
  setOpen: (value: boolean) => void
  children: React.ReactNode
}

const NavBar: React.FC<NavBarProps> = ({ open, setOpen, children }) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const theme = useTheme()
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'))

  const handleDrawerOpen = () => {
    setOpen(true)
  }

  const { 
    userData,
    handleLogout
  } = useContext(AuthContext)

  const handleMenu = (event: MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const isUserEspol = () => {
    return userData.role === 'ADM' || userData.role === 'AS' || userData.role === 'AD'
  }

  const handleClose = () => {
    setAnchorEl(null)
    handleLogout()
    if(userData.role === 'ADM' || userData.role === 'AS' || userData.role === 'AD' )
      window.location.href = 'https://auth.espol.edu.ec/logout'//`${process.env.REACT_APP_API_EXTERNAL_URL}Auth/Logout` 
    else
      window.location.href = '/Login/autenticar'
  }

  return (
    <AppBar position="fixed" open={open}>
      <Toolbar
        sx={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          marginLeft: 2,
          alignContent: 'center',
          justifyContent: 'space-between',
          backgroundColor: 'white',
          borderRadius: open ? 2 : '',
          mt: open? 1 : '',
          mx: open ? 6 : ''
        }}
      >
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            borderBlockColor: themePalette.PRIMARY,
          }}
        >
          
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              sx={{
                marginRight: 1,
                ...(open && { display: 'none' }),
                color: themePalette.PRIMARY
              }}
            >
              <MenuIcon />
            </IconButton>
          
          {
            !isSmallScreen 
              && (children)
          }
        </Box>

         { 
            !isSmallScreen 
              && (<Typography variant="h6" component="div" sx={{ flexGrow: 1, textAlign: 'end' }}>
                    JORGE PITA
                  </Typography>)
        }
        <div>
          <IconButton
            size="large"
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="primary"
          >
            <AccountCircle />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            { isUserEspol() 
              ? <MenuItem onClick={handleClose}>Cerrar sesion</MenuItem>
              // <MenuItem>
              //     <a href={`${process.env.REACT_APP_API_EXTERNAL_URL}Auth/Logout`}>Cerrar sesion1</a>
              //   </MenuItem>
              : <MenuItem onClick={handleClose}>Cerrar sesion</MenuItem>
            }
          </Menu>
        </div>
      </Toolbar>
    </AppBar>
  )
}

export default NavBar
