import { useContext, useEffect, useState } from 'react'
import {
    Autocomplete,
    Box,
    Button,
    FormControl,
    FormControlLabel,
    FormHelperText,
    FormLabel,
    Grid,
    Radio,
    RadioGroup,
    TextField,
    useMediaQuery,
    useTheme
} from '@mui/material'
import { GetLaboratoriesRequestApi, GetLaboratoriesResponseApi } from '../../../types/API/Laboratories'
import { validationSchemaUser } from '../../../constants/ValidationSchemas'
import { initialValueUser } from '../../../constants/FormConstants'
import useFormValidateHook from '../../../hooks/useFormValidateHook'
import { DataForm, valueFormUser } from "../../../types/Form"
import useRequestApi, { MetodoHTTP } from '../../../hooks/useRequestApi'
import { ResponseFormat } from '../../../types/API/Index'
import { RowLaboratories } from '../../../types/DataTable'
import { RegisterRequestApi, RegisterResponseApi } from '../../../types/API/Register'
import { Toastification } from '../../../context/ToastificationContext'


const AddUserForm: React.FC<{}> = () => {

    const [laboratories, setLaboratories] = useState<RowLaboratories[]>([])
    const [disableLaboratory, setDisableLaboratory] = useState<boolean>(true)
    const [searchLaboratory, setSearchLaboratory] = useState<string>()
    
    const requestLaboratories = useRequestApi<ResponseFormat<GetLaboratoriesResponseApi>,GetLaboratoriesRequestApi>({
        path: '/Laboratorio/GetLaboratories',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const requestRegisterUser = useRequestApi<ResponseFormat<RegisterResponseApi>,RegisterRequestApi>({
        path: '/Auth/register/',
        url: process.env.REACT_APP_API_GATEWAY,
        method: MetodoHTTP.POST
    })

    const {
        updateNotificationOpen,
        updateTypeNotification,
        updateMessage
    } = useContext(Toastification)

    const theme = useTheme()
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('md')) 

    const handleSubmit = async (dataForm: DataForm) => {
        const dataFormAux = dataForm as valueFormUser
        const { email, laboratory, name, type: rol } = dataFormAux

        const payload: RegisterRequestApi = {
            email,
            name,
            identification: '0000000000',
            lastName: '',
            password: email,
            telefono: '0000000000',
            rol,
            laboratorio: laboratory?.id??0
        }

        const response = await requestRegisterUser.submit(payload)

        if ( !response ) 
            return
        const { message, succeeded } = response

        if( succeeded ) {
            updateMessage(message)
            updateNotificationOpen(true)
            setTimeout(()=>{
                window.location.href = '/Administrador/usuarios'
            },1000)
        }else {
            updateMessage(message)
            updateTypeNotification(false)
            updateNotificationOpen(true)
        }
       
    }

    const {
        HandleTextFieldChange,
        handleTextFieldBlur,
        handleOtherFieldChange,
        handleFileChange,
        formik
    } = useFormValidateHook({
        initialValues: initialValueUser,
        validationSchema: validationSchemaUser,
        onSubmit: handleSubmit
    }) 

    useEffect(() => {
        const asyncRequestLaboratories = async () => {
            const payload: GetLaboratoriesRequestApi = {
                search: searchLaboratory??'',
                pageNumber: 1,
                pageSize: 10,
                orderById: false,
                orderByName: false,
                orderByStatus: false,
                orderByDescription: false,
                onlyActive: false,
            }
    
            let rows: RowLaboratories[] = []
    
            const response = await requestLaboratories.submit(payload)
    
            if ( response ) {
                const { data } = response
                if ( data ){
                    const { items } = data
                    rows  = items.map<RowLaboratories>(laboratory => {
                        const { nombre, idlablaboratorios, descripcion, deletedAt } = laboratory.laboratorio
                        let row: RowLaboratories = {
                            nombre,
                            descripcion,
                            id: idlablaboratorios,
                            estado: deletedAt ? 'INACTIVO' : 'ACTIVO'
                        }
                        return row
                    })
                }
            }
            setLaboratories(rows)
        }

        asyncRequestLaboratories()
    }, [searchLaboratory])
    

    useEffect(() => {
        setSearchLaboratory('')   
    },[])

  return (
    <form onSubmit={formik.handleSubmit}>
        <Grid container spacing={2}>
            <Grid
                item
                xs={12}
            >
                <Box
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                    }}
                >
                    <Box 
                        sx={{
                            width:"100%",
                            px: 1
                        }}
                    >
                        <TextField
                            id="name"
                            label="Nombre"
                            value={formik.values.name}
                            onChange={HandleTextFieldChange}
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.name && Boolean(formik.errors.name)}
                            helperText={formik.touched.name && formik.errors.name}
                            variant="outlined"
                            fullWidth
                            color="primary"
                        />
                    </Box>
                    <Box 
                        sx={{
                            width: "100%",
                            mt: isSmallScreen ? 4 : 2,
                            px: 1,
                        }}
                    >
                        <TextField
                            id="email"
                            label="Correo"
                            value={formik.values.email}
                            onChange={HandleTextFieldChange}
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.email && Boolean(formik.errors.email)}
                            helperText={formik.touched.email && formik.errors.email}
                            variant="outlined"
                            fullWidth
                            color="primary"
                        />
                        {/* <Autocomplete
                            disablePortal
                            id="email"
                            options={emails}
                            getOptionLabel={(option) => option.email}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    variant="outlined"
                                    label="Correo"
                                    onBlur={handleTextFieldBlur}
                                    error={formik.touched.email && Boolean(formik.errors.email)}
                                    helperText={formik.touched.email && formik.errors.email}
                                />
                            )}
                            onChange={(event, newInputValue: any) => {
                                handleOtherFieldChange(newInputValue,"email")
                            }}
                            fullWidth
                        /> */}
                    </Box>
                    <Box 
                        sx={{
                            width: "100%",
                            mt: isSmallScreen ? 4 : 2,
                            px: 1
                        }}
                    >
                        <Autocomplete
                            disablePortal
                            id="laboratory"
                            disabled={disableLaboratory}
                            options={laboratories}
                            getOptionLabel={(option) => option.nombre}
                            renderInput={(params) => (
                                <TextField 
                            {...params}
                            variant="outlined"
                            label="Laboratorio"
                            onBlur={handleTextFieldBlur}
                            error={formik.touched.laboratory && Boolean(formik.errors.laboratory)}
                            helperText={formik.touched.laboratory && formik.errors.laboratory}
                                />
                            )}
                            onChange={(event, newInputValue: RowLaboratories | null) => {
                                handleOtherFieldChange(newInputValue,"laboratory")
                            }}
                            fullWidth
                        />
                    </Box>
                    <Box 
                        sx={{
                            width: "100%",
                            mt: isSmallScreen ? 4 : 2,
                            px: 1
                        }}
                    >
                        <FormControl>
                            <FormLabel id="type-user">Tipo Usuario</FormLabel>
                            <RadioGroup
                                row
                                aria-labelledby="type-user"
                                value={formik.values.type}
                                onChange={(evenet, newValue) => {
                                    setDisableLaboratory(true)
                                    if( newValue === 'AD' )
                                        setDisableLaboratory(false)
                                    handleOtherFieldChange(newValue,"type")
                                }}
                            >
                                <FormControlLabel value={'AS'} control={<Radio />} label="Asistente Silab" />
                                <FormControlLabel value={'AD'} control={<Radio />} label="Asistente Laboratorio" />
                                <FormControlLabel value={'ADM'} control={<Radio />} label="Administrador" />
                            </RadioGroup>
                            {formik.touched.type && formik.errors.type && (
                                <FormHelperText>{formik.errors.type}</FormHelperText>
                            )}
                        </FormControl>
                    </Box>
                </Box>
            </Grid>         
        </Grid>
        <Button 
                type="submit"
                variant="contained"
                color="primary"
                sx={{ 
                    mt: 2,
                    height: "60px",
                    width: '50%',
                }}
            >
                Guardar
        </Button>
    </form>
  )
}

export default AddUserForm