import { useEffect, useState } from "react"
import { reqResApi } from "../config/api/Request.Config"

export enum MetodoHTTP {
    GET = "GET",
    POST = "POST",
    PUT = "PUT",
    DELETE = "DELETE",
    PATCH = "PATCH",
    HEAD = "HEAD",
    OPTIONS = "OPTIONS",
  }
  

type UseRequestApi<T,K> = {
    path: string
    url: string | undefined,
    method: MetodoHTTP
}

const  headers = {
    'Content-Type': 'multipart/form-data',
}

function useRequestImages<T,K>({path, url, method}: UseRequestApi<T,K>) {

    const [apiUrl, setApiUrl] = useState<string>()

    useEffect(() => {
        setApiUrl(url)
    }, [url,path])

    const postRequest = async (payload: K) => {
        if (!apiUrl)
            return
        const res = await reqResApi(apiUrl).post<T>(path, 
            payload,
            {
                headers
            }    
        )
        return res.data
    }

    const getRequest = async (payload: K) => {
        if (!apiUrl)
            return
        const res = await reqResApi(apiUrl).get<T>(path+payload,{
            responseType: 'arraybuffer',
          })
        return res.data
    }

    
    const submit = async (payload: K) => {
        if (!apiUrl)
            return

        switch (method) {
            case MetodoHTTP.POST:
                return postRequest(payload)
            case MetodoHTTP.GET:
                return getRequest(payload)
            default:
                break;
        }
        
    }

    return {
        submit
    }
}

export default useRequestImages